import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import {
  CheckCircle,
  X,
  Eye,
  ThumbsUp,
  ThumbsDown,
  Clock,
  AlertTriangle,
  Calendar,
  Tag,
  FileImage
} from 'lucide-react';
import { toast } from 'sonner';

import { Card, CardContent } from '../components/ui/Card';
import { Button } from '../components/ui/Button';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '../components/ui/Dialog';
import { Skeleton } from '../components/ui/Skeleton';
import { imagesAPI, getImageUrl } from '../services/api';

function ReviewPage() {
  const [selectedImage, setSelectedImage] = useState(null);
  const queryClient = useQueryClient();

  // Fetch pending images
  const { data: pendingImages = [], isLoading, error } = useQuery({
    queryKey: ['images', 'PENDING'],
    queryFn: () => imagesAPI.getAll('PENDING'),
    select: (response) => response.data || []
  });

  // Approve image mutation
  const approveMutation = useMutation({
    mutationFn: (imageId) => imagesAPI.approve(imageId),
    onSuccess: (_, imageId) => {
      queryClient.invalidateQueries(['images']);
      setSelectedImage(null);
      toast.success('Grsel baaryla onayland! ');
    },
    onError: (error) => {
      console.error('Approval error:', error);
      toast.error('Onaylama srasnda hata olutu');
    }
  });

  // Reject image mutation
  const rejectMutation = useMutation({
    mutationFn: (imageId) => imagesAPI.reject(imageId),
    onSuccess: (_, imageId) => {
      queryClient.invalidateQueries(['images']);
      setSelectedImage(null);
      toast.success('Grsel reddedildi');
    },
    onError: (error) => {
      console.error('Rejection error:', error);
      toast.error('Reddetme srasnda hata olutu');
    }
  });

  const handleApprove = (imageId, event) => {
    event?.stopPropagation();
    approveMutation.mutate(imageId);
  };

  const handleReject = (imageId, event) => {
    event?.stopPropagation();
    if (window.confirm('Bu grseli reddetmek istediinizden emin misiniz? Grsel silinecektir.')) {
      rejectMutation.mutate(imageId);
    }
  };

  if (error) {
    return (
      <div className="text-center py-12">
        <div className="bg-red-50 border border-red-200 rounded-lg p-6 max-w-md mx-auto">
          <h3 className="text-lg font-medium text-red-800 mb-2">Hata Olutu</h3>
          <p className="text-red-600">Onay bekleyen grseller yklenirken bir hata olutu.</p>
          <Button
            variant="outline"
            onClick={() => window.location.reload()}
            className="mt-4"
          >
            Tekrar Dene
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto space-y-6">
      {/* Header */}
      <motion.div
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        className="text-center"
      >
        <div className="flex items-center justify-center mb-4">
          <div className="h-12 w-12 bg-gradient-to-r from-green-500 to-emerald-500 rounded-xl flex items-center justify-center mr-3">
            <CheckCircle className="h-6 w-6 text-white" />
          </div>
          <h1 className="text-3xl font-bold text-gray-900">Onay Paneli</h1>
        </div>
        <p className="text-gray-600">
          Onay bekleyen grselleri inceleyin ve karara balayn
        </p>
      </motion.div>

      {/* Stats Card */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.1 }}
      >
        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-4">
                <div className="h-12 w-12 bg-orange-100 rounded-lg flex items-center justify-center">
                  <Clock className="h-6 w-6 text-orange-600" />
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-gray-900">
                    Onay Bekleyen Grseller
                  </h3>
                  <p className="text-gray-600">
                    {isLoading ? 'Ykleniyor...' : `${pendingImages.length} grsel inceleme bekliyor`}
                  </p>
                </div>
              </div>

              {pendingImages.length > 0 && (
                <div className="text-right">
                  <div className="text-2xl font-bold text-orange-600">
                    {pendingImages.length}
                  </div>
                  <div className="text-sm text-gray-500">beklemede</div>
                </div>
              )}
            </div>
          </CardContent>
        </Card>
      </motion.div>

      {/* Images Grid */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.2 }}
      >
        {isLoading ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {Array.from({ length: 6 }).map((_, i) => (
              <Card key={i}>
                <CardContent className="p-4">
                  <Skeleton className="aspect-square w-full rounded-lg mb-4" />
                  <Skeleton className="h-4 w-3/4 mb-2" />
                  <Skeleton className="h-3 w-1/2 mb-4" />
                  <div className="flex gap-2">
                    <Skeleton className="h-9 flex-1" />
                    <Skeleton className="h-9 flex-1" />
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        ) : pendingImages.length === 0 ? (
          <div className="text-center py-12">
            <CheckCircle className="h-16 w-16 text-green-300 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-gray-900 mb-2">Tm Grseller ncelendi!</h3>
            <p className="text-gray-600">
              u anda onay bekleyen grsel bulunmuyor.
            </p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            <AnimatePresence>
              {pendingImages.map((image, index) => (
                <motion.div
                  key={image.id}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, scale: 0.9 }}
                  transition={{ delay: index * 0.05 }}
                  layout
                >
                  <Card className="group hover:shadow-lg transition-all duration-300">
                    <CardContent className="p-0">
                      {/* Image */}
                      <div
                        className="relative aspect-square bg-gray-100 rounded-t-lg overflow-hidden cursor-pointer"
                        onClick={() => setSelectedImage(image)}
                      >
                        <img
                          src={getImageUrl(image.filePath)}
                          alt={image.prompt}
                          className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                        />

                        {/* Pending Badge */}
                        <div className="absolute top-2 left-2 bg-orange-500 text-white text-xs px-2 py-1 rounded-full flex items-center">
                          <Clock className="h-3 w-3 mr-1" />
                          Beklemede
                        </div>

                        {/* Quick View Overlay */}
                        <div className="absolute inset-0 bg-black/0 group-hover:bg-black/20 transition-colors duration-300 flex items-center justify-center">
                          <Button
                            size="sm"
                            variant="secondary"
                            className="opacity-0 group-hover:opacity-100 transition-opacity duration-300"
                            onClick={(e) => {
                              e.stopPropagation();
                              setSelectedImage(image);
                            }}
                          >
                            <Eye className="h-4 w-4 mr-1" />
                            ncele
                          </Button>
                        </div>

                        {/* Grade Badge */}
                        {image.metadata?.snf && (
                          <div className="absolute top-2 right-2 bg-blue-500 text-white text-xs px-2 py-1 rounded-full">
                            {image.metadata.snf}. Snf
                          </div>
                        )}
                      </div>

                      {/* Content */}
                      <div className="p-4">
                        <h3 className="font-medium text-gray-900 mb-2 line-clamp-2">
                          {image.prompt.length > 60
                            ? `${image.prompt.substring(0, 60)}...`
                            : image.prompt}
                        </h3>

                        <div className="space-y-1 mb-4">
                          {image.metadata?.konu && (
                            <div className="flex items-center text-xs text-gray-500">
                              <Tag className="h-3 w-3 mr-1" />
                              {image.metadata.konu}
                            </div>
                          )}

                          <div className="flex items-center text-xs text-gray-400">
                            <Calendar className="h-3 w-3 mr-1" />
                            {new Date(image.createdAt).toLocaleDateString('tr-TR')}
                          </div>
                        </div>

                        {/* Action Buttons */}
                        <div className="flex gap-2">
                          <Button
                            size="sm"
                            className="flex-1 bg-gradient-to-r from-green-600 to-emerald-600 hover:from-green-700 hover:to-emerald-700"
                            onClick={(e) => handleApprove(image.id, e)}
                            disabled={approveMutation.isPending || rejectMutation.isPending}
                          >
                            <ThumbsUp className="h-4 w-4 mr-1" />
                            Onayla
                          </Button>

                          <Button
                            size="sm"
                            variant="destructive"
                            className="flex-1"
                            onClick={(e) => handleReject(image.id, e)}
                            disabled={approveMutation.isPending || rejectMutation.isPending}
                          >
                            <ThumbsDown className="h-4 w-4 mr-1" />
                            Reddet
                          </Button>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                </motion.div>
              ))}
            </AnimatePresence>
          </div>
        )}
      </motion.div>

      {/* Image Detail Modal */}
      <Dialog open={!!selectedImage} onOpenChange={() => setSelectedImage(null)}>
        <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
          {selectedImage && (
            <>
              <DialogHeader>
                <DialogTitle className="flex items-center justify-between">
                  Grsel nceleme
                  <div className="flex items-center space-x-2">
                    <div className="flex items-center bg-orange-100 text-orange-800 text-xs px-2 py-1 rounded-full">
                      <Clock className="h-3 w-3 mr-1" />
                      Onay Bekliyor
                    </div>
                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={() => setSelectedImage(null)}
                    >
                      <X className="h-4 w-4" />
                    </Button>
                  </div>
                </DialogTitle>
              </DialogHeader>

              <div className="grid md:grid-cols-2 gap-6">
                {/* Image */}
                <div className="space-y-4">
                  <div className="aspect-square bg-gray-100 rounded-lg overflow-hidden">
                    <img
                      src={getImageUrl(selectedImage.filePath)}
                      alt={selectedImage.prompt}
                      className="w-full h-full object-cover"
                    />
                  </div>

                  {/* Action Buttons */}
                  <div className="flex gap-3">
                    <Button
                      onClick={(e) => handleApprove(selectedImage.id, e)}
                      className="flex-1 bg-gradient-to-r from-green-600 to-emerald-600 hover:from-green-700 hover:to-emerald-700"
                      disabled={approveMutation.isPending || rejectMutation.isPending}
                    >
                      <ThumbsUp className="mr-2 h-4 w-4" />
                      Onayla
                    </Button>

                    <Button
                      variant="destructive"
                      onClick={(e) => handleReject(selectedImage.id, e)}
                      className="flex-1"
                      disabled={approveMutation.isPending || rejectMutation.isPending}
                    >
                      <ThumbsDown className="mr-2 h-4 w-4" />
                      Reddet
                    </Button>
                  </div>
                </div>

                {/* Details */}
                <div className="space-y-6">
                  <div>
                    <h3 className="font-semibold text-lg mb-2">Grsel Aklamas</h3>
                    <p className="text-gray-700 bg-gray-50 p-4 rounded-lg">
                      {selectedImage.prompt}
                    </p>
                  </div>

                  {selectedImage.metadata && (
                    <div>
                      <h3 className="font-semibold text-lg mb-3">Eitim Bilgileri</h3>
                      <div className="space-y-3">
                        {selectedImage.metadata.snf && (
                          <div className="flex items-center p-3 bg-blue-50 rounded-lg">
                            <div className="font-medium text-blue-700 w-20">Snf:</div>
                            <div className="text-blue-600">{selectedImage.metadata.snf}</div>
                          </div>
                        )}

                        {selectedImage.metadata.nite && (
                          <div className="flex items-center p-3 bg-purple-50 rounded-lg">
                            <div className="font-medium text-purple-700 w-20">nite:</div>
                            <div className="text-purple-600">{selectedImage.metadata.nite}</div>
                          </div>
                        )}

                        {selectedImage.metadata.konu && (
                          <div className="flex items-center p-3 bg-green-50 rounded-lg">
                            <div className="font-medium text-green-700 w-20">Konu:</div>
                            <div className="text-green-600">{selectedImage.metadata.konu}</div>
                          </div>
                        )}

                        {selectedImage.metadata.kazanm && (
                          <div className="p-3 bg-orange-50 rounded-lg">
                            <div className="font-medium text-orange-700 mb-2">Kazanm:</div>
                            <div className="text-orange-600 text-sm leading-relaxed">
                              {selectedImage.metadata.kazanm}
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  )}

                  <div>
                    <h3 className="font-semibold text-lg mb-3">Sistem Bilgileri</h3>
                    <div className="space-y-2 text-sm">
                      <div className="flex items-center p-2 bg-gray-50 rounded">
                        <Calendar className="h-4 w-4 mr-2 text-gray-400" />
                        <span className="text-gray-600">
                          Oluturulma: {new Date(selectedImage.createdAt).toLocaleString('tr-TR')}
                        </span>
                      </div>

                      <div className="flex items-center p-2 bg-gray-50 rounded">
                        <FileImage className="h-4 w-4 mr-2 text-gray-400" />
                        <span className="text-gray-600">ID: {selectedImage.id}</span>
                      </div>
                    </div>
                  </div>

                  {/* Quality Check */}
                  <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
                    <div className="flex items-center mb-2">
                      <AlertTriangle className="h-5 w-5 text-yellow-600 mr-2" />
                      <h4 className="font-medium text-yellow-800">nceleme Kriterleri</h4>
                    </div>
                    <ul className="text-sm text-yellow-700 space-y-1">
                      <li> Grsel eitim materyali iin uygun mu?</li>
                      <li> erik ocuklar iin gvenli mi?</li>
                      <li> Grsel kalitesi yeterli mi?</li>
                      <li> Aklama ile grsel uyumlu mu?</li>
                    </ul>
                  </div>
                </div>
              </div>
            </>
          )}
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default ReviewPage;

