import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import {
  FileText,
  Plus,
  Edit,
  Trash2,
  Save,
  Tag,
  Sparkles,
  Copy
} from 'lucide-react';
import { toast } from 'sonner';

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../components/ui/Card';
import { Button } from '../components/ui/Button';
import { Input, Textarea } from '../components/ui/Input';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '../components/ui/Dialog';
import { Skeleton } from '../components/ui/Skeleton';
import { templatesAPI } from '../services/api';

// Form validation schema
const templateSchema = z.object({
  templateText: z.string().min(10, 'ablon metni en az 10 karakter olmaldr'),
  description: z.string().optional(),
  variables: z.string().optional(),
});

function TemplatePage() {
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [editingTemplate, setEditingTemplate] = useState(null);
  const queryClient = useQueryClient();

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
    setValue,
    watch
  } = useForm({
    resolver: zodResolver(templateSchema),
    defaultValues: {
      templateText: '',
      description: '',
      variables: '',
    }
  });

  // Fetch templates
  const { data: templates = [], isLoading, error } = useQuery({
    queryKey: ['templates'],
    queryFn: () => templatesAPI.getAll(),
    select: (response) => response.data || []
  });

  // Create template mutation
  const createTemplateMutation = useMutation({
    mutationFn: (data) => templatesAPI.create(data),
    onSuccess: () => {
      queryClient.invalidateQueries(['templates']);
      setIsDialogOpen(false);
      setEditingTemplate(null);
      reset();
      toast.success('ablon baaryla oluturuldu!');
    },
    onError: (error) => {
      console.error('Template creation error:', error);
      toast.error('ablon oluturulurken hata olutu');
    }
  });

  // Update template mutation
  const updateTemplateMutation = useMutation({
    mutationFn: ({ id, data }) => templatesAPI.update(id, data),
    onSuccess: () => {
      queryClient.invalidateQueries(['templates']);
      setIsDialogOpen(false);
      setEditingTemplate(null);
      reset();
      toast.success('ablon baaryla gncellendi!');
    },
    onError: (error) => {
      console.error('Template update error:', error);
      toast.error('ablon gncellenirken hata olutu');
    }
  });

  // Delete template mutation
  const deleteTemplateMutation = useMutation({
    mutationFn: (id) => templatesAPI.delete(id),
    onSuccess: () => {
      queryClient.invalidateQueries(['templates']);
      toast.success('ablon baaryla silindi!');
    },
    onError: (error) => {
      console.error('Template deletion error:', error);
      toast.error('ablon silinirken hata olutu');
    }
  });

  // Predefined variables
  const commonVariables = ['snf', 'nite', 'konu', 'kazanm', 'materyali', 'seviye'];

  const handleEdit = (template) => {
    setEditingTemplate(template);
    setValue('templateText', template.templateText);
    setValue('description', template.description || '');
    setValue('variables', template.variables || '');
    setIsDialogOpen(true);
  };

  const handleDelete = (id) => {
    if (window.confirm('Bu ablonu silmek istediinizden emin misiniz?')) {
      deleteTemplateMutation.mutate(id);
    }
  };

  const onSubmit = (data) => {
    // Parse variables
    const processedData = {
      ...data,
      variables: data.variables ? data.variables.split(',').map(v => v.trim()).filter(Boolean) : []
    };

    if (editingTemplate) {
      updateTemplateMutation.mutate({ id: editingTemplate.id, data: processedData });
    } else {
      createTemplateMutation.mutate(processedData);
    }
  };

  const insertVariable = (variable) => {
    const currentText = watch('templateText') || '';
    const newText = currentText + `{{${variable}}}`;
    setValue('templateText', newText);
  };

  const copyTemplate = (template) => {
    navigator.clipboard.writeText(template.templateText);
    toast.success('ablon panoya kopyaland!');
  };

  // Extract variables from template text
  const extractVariables = (text) => {
    const regex = /\{\{(.*?)\}\}/g;
    const matches = [];
    let match;
    while ((match = regex.exec(text)) !== null) {
      if (!matches.includes(match[1])) {
        matches.push(match[1]);
      }
    }
    return matches;
  };

  if (error) {
    return (
      <div className="text-center py-12">
        <div className="bg-red-50 border border-red-200 rounded-lg p-6 max-w-md mx-auto">
          <h3 className="text-lg font-medium text-red-800 mb-2">Hata Olutu</h3>
          <p className="text-red-600">ablonlar yklenirken bir hata olutu.</p>
          <Button
            variant="outline"
            onClick={() => window.location.reload()}
            className="mt-4"
          >
            Tekrar Dene
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto space-y-6">
      {/* Header */}
      <motion.div
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        className="flex items-center justify-between"
      >
        <div>
          <div className="flex items-center mb-4">
            <div className="h-12 w-12 bg-gradient-to-r from-orange-500 to-red-500 rounded-xl flex items-center justify-center mr-3">
              <FileText className="h-6 w-6 text-white" />
            </div>
            <h1 className="text-3xl font-bold text-gray-900">ablon Ynetimi</h1>
          </div>
          <p className="text-gray-600">
            Resim retimi iin ablonlar oluturun ve ynetin
          </p>
        </div>

        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button
              className="bg-gradient-to-r from-orange-600 to-red-600 hover:from-orange-700 hover:to-red-700"
              onClick={() => {
                setEditingTemplate(null);
                reset();
              }}
            >
              <Plus className="mr-2 h-4 w-4" />
              Yeni ablon
            </Button>
          </DialogTrigger>

          <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
            <DialogHeader>
              <DialogTitle>
                {editingTemplate ? 'ablon Dzenle' : 'Yeni ablon Olutur'}
              </DialogTitle>
            </DialogHeader>

            <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
              <div className="grid md:grid-cols-2 gap-6">
                {/* Form Fields */}
                <div className="space-y-4">
                  <div className="space-y-2">
                    <label className="text-sm font-medium">ablon Metni</label>
                    <Textarea
                      {...register('templateText')}
                      placeholder="{{snf}}. snf {{konu}} iin eitici grsel..."
                      className="min-h-[120px] resize-none"
                      disabled={createTemplateMutation.isPending || updateTemplateMutation.isPending}
                    />
                    {errors.templateText && (
                      <p className="text-sm text-red-500">{errors.templateText.message}</p>
                    )}
                  </div>

                  <div className="space-y-2">
                    <label className="text-sm font-medium">Aklama (Opsiyonel)</label>
                    <Input
                      {...register('description')}
                      placeholder="Bu ablonun ne iin kullanldn aklayn..."
                      disabled={createTemplateMutation.isPending || updateTemplateMutation.isPending}
                    />
                  </div>

                  <div className="space-y-2">
                    <label className="text-sm font-medium">Deikenler (virglle ayrn)</label>
                    <Input
                      {...register('variables')}
                      placeholder="snf, konu, nite, kazanm"
                      disabled={createTemplateMutation.isPending || updateTemplateMutation.isPending}
                    />
                    <p className="text-xs text-gray-500">
                      ablonunuzda kullandnz deikenleri listeleyin
                    </p>
                  </div>
                </div>

                {/* Helper Panel */}
                <div className="space-y-4">
                  <div>
                    <h4 className="font-medium mb-3">Hazr Deikenler</h4>
                    <div className="grid grid-cols-2 gap-2">
                      {commonVariables.map((variable) => (
                        <Button
                          key={variable}
                          type="button"
                          variant="outline"
                          size="sm"
                          onClick={() => insertVariable(variable)}
                          className="justify-start"
                        >
                          <Tag className="h-3 w-3 mr-1" />
                          {variable}
                        </Button>
                      ))}
                    </div>
                  </div>

                  {watch('templateText') && (
                    <div>
                      <h4 className="font-medium mb-2">nizleme</h4>
                      <div className="bg-gray-50 p-3 rounded-lg">
                        <p className="text-sm font-mono">{watch('templateText')}</p>
                      </div>

                      <div className="mt-2">
                        <p className="text-xs text-gray-600 mb-1">Tespit edilen deikenler:</p>
                        <div className="flex flex-wrap gap-1">
                          {extractVariables(watch('templateText')).map((variable, index) => (
                            <span
                              key={index}
                              className="text-xs bg-blue-100 text-blue-700 px-2 py-0.5 rounded"
                            >
                              {variable}
                            </span>
                          ))}
                        </div>
                      </div>
                    </div>
                  )}
                </div>
              </div>

              <div className="flex justify-end space-x-3">
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => {
                    setIsDialogOpen(false);
                    setEditingTemplate(null);
                    reset();
                  }}
                >
                  ptal
                </Button>

                <Button
                  type="submit"
                  disabled={createTemplateMutation.isPending || updateTemplateMutation.isPending}
                  className="bg-gradient-to-r from-orange-600 to-red-600 hover:from-orange-700 hover:to-red-700"
                >
                  {(createTemplateMutation.isPending || updateTemplateMutation.isPending) ? (
                    <>
                      <motion.div
                        className="mr-2 h-4 w-4 border-2 border-white border-t-transparent rounded-full"
                        animate={{ rotate: 360 }}
                        transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                      />
                      Kaydediliyor...
                    </>
                  ) : (
                    <>
                      <Save className="mr-2 h-4 w-4" />
                      {editingTemplate ? 'Gncelle' : 'Olutur'}
                    </>
                  )}
                </Button>
              </div>
            </form>
          </DialogContent>
        </Dialog>
      </motion.div>

      {/* Templates Grid */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.1 }}
      >
        {isLoading ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {Array.from({ length: 6 }).map((_, i) => (
              <Card key={i}>
                <CardContent className="p-4">
                  <Skeleton className="h-4 w-3/4 mb-2" />
                  <Skeleton className="h-20 w-full mb-4" />
                  <div className="flex gap-2">
                    <Skeleton className="h-8 flex-1" />
                    <Skeleton className="h-8 w-16" />
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        ) : templates.length === 0 ? (
          <div className="text-center py-12">
            <FileText className="h-16 w-16 text-gray-300 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-gray-900 mb-2">Henz ablon Yok</h3>
            <p className="text-gray-600 mb-4">
              Resim retimini hzlandrmak iin ablonlar oluturun.
            </p>
            <Button
              onClick={() => setIsDialogOpen(true)}
              className="bg-gradient-to-r from-orange-600 to-red-600 hover:from-orange-700 hover:to-red-700"
            >
              <Plus className="mr-2 h-4 w-4" />
              lk ablonunu Olutur
            </Button>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            <AnimatePresence>
              {templates.map((template, index) => (
                <motion.div
                  key={template.id}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, scale: 0.9 }}
                  transition={{ delay: index * 0.05 }}
                  layout
                >
                  <Card className="group hover:shadow-lg transition-all duration-300">
                    <CardHeader>
                      <CardTitle className="flex items-center justify-between text-base">
                        <div className="flex items-center">
                          <Sparkles className="h-4 w-4 mr-2 text-orange-500" />
                          ablon #{template.id}
                        </div>
                        <div className="flex space-x-1">
                          <Button
                            size="sm"
                            variant="ghost"
                            onClick={() => copyTemplate(template)}
                            className="h-8 w-8 p-0 hover:bg-blue-50 hover:text-blue-600"
                          >
                            <Copy className="h-3 w-3" />
                          </Button>
                          <Button
                            size="sm"
                            variant="ghost"
                            onClick={() => handleEdit(template)}
                            className="h-8 w-8 p-0 hover:bg-yellow-50 hover:text-yellow-600"
                          >
                            <Edit className="h-3 w-3" />
                          </Button>
                          <Button
                            size="sm"
                            variant="ghost"
                            onClick={() => handleDelete(template.id)}
                            className="h-8 w-8 p-0 hover:bg-red-50 hover:text-red-600"
                            disabled={deleteTemplateMutation.isPending}
                          >
                            <Trash2 className="h-3 w-3" />
                          </Button>
                        </div>
                      </CardTitle>
                      {template.description && (
                        <CardDescription>{template.description}</CardDescription>
                      )}
                    </CardHeader>

                    <CardContent>
                      <div className="bg-gray-50 p-3 rounded-lg mb-4">
                        <p className="text-sm font-mono text-gray-700 leading-relaxed">
                          {template.templateText.length > 120
                            ? `${template.templateText.substring(0, 120)}...`
                            : template.templateText}
                        </p>
                      </div>

                      {extractVariables(template.templateText).length > 0 && (
                        <div>
                          <p className="text-xs font-medium text-gray-600 mb-2">Deikenler:</p>
                          <div className="flex flex-wrap gap-1">
                            {extractVariables(template.templateText).map((variable, idx) => (
                              <span
                                key={idx}
                                className="text-xs bg-indigo-100 text-indigo-700 px-2 py-0.5 rounded-full"
                              >
                                {variable}
                              </span>
                            ))}
                          </div>
                        </div>
                      )}

                      <div className="mt-4 pt-3 border-t flex justify-between items-center text-xs text-gray-500">
                        <span>
                          {new Date(template.createdAt).toLocaleDateString('tr-TR')}
                        </span>
                        <span className="bg-green-100 text-green-700 px-2 py-0.5 rounded-full">
                          Aktif
                        </span>
                      </div>
                    </CardContent>
                  </Card>
                </motion.div>
              ))}
            </AnimatePresence>
          </div>
        )}
      </motion.div>
    </div>
  );
}

export default TemplatePage;
