import React, { useState } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import {
  Palette,
  CheckCircle,
  ImageIcon,
  FileText,
  Users,
  LogOut,
  Sparkles,
  ChevronLeft,
  ChevronRight,
  Menu,
  X,
  GraduationCap,
  BookOpenCheck,
  School
} from 'lucide-react';

import useAuthStore from '../store/authStore';
import { Button } from './ui/Button';
import { cn } from '../lib/utils';

function Sidebar() {
  const { isAuthenticated, user, logout, hasRole } = useAuthStore();
  const location = useLocation();
  const navigate = useNavigate();
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [isMobileOpen, setIsMobileOpen] = useState(false);

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  const navItems = [
    {
      to: '/',
      label: 'Resim ret',
      icon: Palette,
      requiredRole: 'TEACHER',
      gradient: 'from-purple-500 to-pink-500',
      description: 'AI ile grsel olutur'
    },
    {
      to: '/review',
      label: 'Onay Paneli',
      icon: CheckCircle,
      requiredRole: 'TEACHER',
      gradient: 'from-green-500 to-emerald-500',
      description: 'Grselleri onayla'
    },
    {
      to: '/gallery',
      label: 'Galeri',
      icon: ImageIcon,
      requiredRole: null,
      gradient: 'from-blue-500 to-cyan-500',
      description: 'Onayl grseller'
    },
    {
      to: '/teacher',
      label: 'Snav Olutur',
      icon: GraduationCap,
      requiredRole: 'TEACHER',
      gradient: 'from-teal-500 to-cyan-500',
      description: 'AI ile snav hazrla'
    },
    {
      to: '/classrooms',
      label: 'Snflarm',
      icon: School,
      requiredRole: 'TEACHER',
      gradient: 'from-indigo-500 to-purple-500',
      description: 'Snf ynetimi'
    },
    {
      to: '/student',
      label: 'devlerim',
      icon: BookOpenCheck,
      requiredRole: 'STUDENT',
      gradient: 'from-amber-500 to-orange-500',
      description: 'Snavlar z'
    },
    {
      to: '/templates',
      label: 'ablonlar',
      icon: FileText,
      requiredRole: 'ADMIN',
      gradient: 'from-orange-500 to-red-500',
      description: 'Prompt ablonlar'
    },
    {
      to: '/admin',
      label: 'Kullanclar',
      icon: Users,
      requiredRole: 'ADMIN',
      gradient: 'from-red-500 to-pink-500',
      description: 'Kullanc ynetimi'
    }
  ];

  const filteredNavItems = isAuthenticated ? navItems.filter(item =>
    !item.requiredRole || hasRole(item.requiredRole)
  ) : [];

  const handleNavClick = (to) => {
    navigate(to);
    setIsMobileOpen(false);
  };

  const SidebarContent = () => (
    <div className="flex flex-col h-full w-full overflow-hidden">
      {/* Logo Section */}
      <div className={cn(
        "flex items-center px-4 py-5 border-b border-gray-200/50",
        isCollapsed ? "justify-center" : "justify-between"
      )}>
        {!isCollapsed && (
          <Link to="/" className="flex items-center gap-2 group overflow-hidden">
            <div className="relative flex-shrink-0">
              <Sparkles className="h-7 w-7 text-indigo-600 group-hover:text-indigo-700 transition-colors" />
              <div className="absolute inset-0 bg-indigo-200 rounded-full blur-md opacity-0 group-hover:opacity-50 transition-opacity" />
            </div>
            <span className="text-lg font-bold bg-gradient-to-r from-indigo-600 to-purple-600 bg-clip-text text-transparent truncate">
              BilGen AI
            </span>
          </Link>
        )}

        {/* Desktop Collapse Button */}
        <button
          onClick={() => setIsCollapsed(!isCollapsed)}
          className="hidden lg:flex items-center justify-center h-8 w-8 rounded-lg hover:bg-gray-100 transition-colors"
        >
          {isCollapsed ? (
            <ChevronRight className="h-5 w-5 text-gray-600" />
          ) : (
            <ChevronLeft className="h-5 w-5 text-gray-600" />
          )}
        </button>

        {/* Mobile Close Button */}
        <button
          onClick={() => setIsMobileOpen(false)}
          className="lg:hidden flex items-center justify-center h-8 w-8 rounded-lg hover:bg-gray-100 transition-colors"
        >
          <X className="h-5 w-5 text-gray-600" />
        </button>
      </div>

      {/* User Info */}
      {isAuthenticated && (
        <div className={cn(
          "px-4 py-5 border-b border-gray-200/50",
          isCollapsed && "px-3"
        )}>
          <div className={cn(
            "flex items-center gap-3",
            isCollapsed && "flex-col gap-2"
          )}>
            <div className="h-10 w-10 bg-gradient-to-r from-indigo-500 to-purple-500 rounded-full flex items-center justify-center text-white text-sm font-medium flex-shrink-0">
              {(user?.name || user?.email)?.charAt(0).toUpperCase()}
            </div>

            {!isCollapsed && (
              <div className="flex-1 min-w-0 overflow-hidden">
                <p className="text-sm font-semibold text-gray-900 truncate">
                  {user?.name || user?.email}
                </p>
                <p className="text-xs text-gray-500 truncate capitalize">
                  {user?.roles?.join(', ').toLowerCase()}
                </p>
              </div>
            )}
          </div>
        </div>
      )}

      {/* Navigation Items */}
      <nav className="flex-1 px-3 py-4 space-y-1 overflow-y-auto overflow-x-hidden">
        {filteredNavItems.map((item) => {
          const Icon = item.icon;
          const isActive = location.pathname === item.to;

          return (
            <motion.button
              key={item.to}
              onClick={() => handleNavClick(item.to)}
              className={cn(
                "w-full group relative flex items-center px-3 py-2.5 rounded-xl transition-all duration-200",
                isActive
                  ? "bg-gradient-to-r " + item.gradient + " text-white shadow-md"
                  : "text-gray-600 hover:bg-gray-50 hover:text-gray-900",
                isCollapsed && "justify-center px-2"
              )}
              whileHover={{ x: isCollapsed ? 0 : 2 }}
              whileTap={{ scale: 0.98 }}
            >
              <Icon className={cn(
                "flex-shrink-0",
                isCollapsed ? "h-6 w-6" : "h-5 w-5"
              )} />

              {!isCollapsed && (
                <div className="ml-3 flex-1 text-left">
                  <p className="text-sm font-medium">{item.label}</p>
                  {!isActive && (
                    <p className="text-xs opacity-60">{item.description}</p>
                  )}
                </div>
              )}

              {/* Active Indicator */}
              {isActive && !isCollapsed && (
                <motion.div
                  className="absolute right-3 top-1/2 -translate-y-1/2 h-1.5 w-1.5 bg-white rounded-full"
                  layoutId="activeIndicator"
                  transition={{
                    type: "spring",
                    stiffness: 500,
                    damping: 30
                  }}
                />
              )}

              {/* Tooltip for collapsed state */}
              {isCollapsed && (
                <div className="absolute left-full ml-2 px-3 py-2 bg-gray-900 text-white text-sm rounded-lg opacity-0 group-hover:opacity-100 pointer-events-none transition-opacity whitespace-nowrap z-50">
                  <div className="font-medium">{item.label}</div>
                  <div className="text-xs text-gray-300">{item.description}</div>
                  <div className="absolute right-full top-1/2 -translate-y-1/2 border-4 border-transparent border-r-gray-900" />
                </div>
              )}
            </motion.button>
          );
        })}
      </nav>

      {/* Logout Button */}
      {isAuthenticated && (
        <div className="px-3 py-3 border-t border-gray-200/50">
          <Button
            variant="ghost"
            onClick={handleLogout}
            className={cn(
              "w-full text-red-600 hover:text-red-700 hover:bg-red-50 justify-start",
              isCollapsed && "justify-center px-2"
            )}
          >
            <LogOut className={cn(
              "flex-shrink-0",
              isCollapsed ? "h-5 w-5" : "h-4 w-4 mr-2"
            )} />
            {!isCollapsed && "k Yap"}
          </Button>
        </div>
      )}

      {/* Footer */}
      {!isCollapsed && (
        <div className="px-4 py-3 text-center text-xs text-gray-500">
          <p> 2026 BilGen AI</p>
        </div>
      )}
    </div>
  );

  return (
    <>
      {/* Mobile Menu Button */}
      {isAuthenticated && (
        <button
          onClick={() => setIsMobileOpen(true)}
          className="lg:hidden fixed top-4 left-4 z-40 flex items-center justify-center h-10 w-10 rounded-lg bg-white shadow-lg hover:shadow-xl transition-all"
        >
          <Menu className="h-5 w-5 text-gray-700" />
        </button>
      )}

      {/* Mobile Overlay */}
      <AnimatePresence>
        {isMobileOpen && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="lg:hidden fixed inset-0 bg-black/50 backdrop-blur-sm z-40"
            onClick={() => setIsMobileOpen(false)}
          />
        )}
      </AnimatePresence>

      {/* Mobile Sidebar */}
      <AnimatePresence>
        {isMobileOpen && (
          <motion.aside
            initial={{ x: -300 }}
            animate={{ x: 0 }}
            exit={{ x: -300 }}
            transition={{ type: "spring", damping: 30, stiffness: 300 }}
            className="lg:hidden fixed left-0 top-0 bottom-0 w-72 bg-white shadow-2xl z-50 overflow-hidden"
          >
            <SidebarContent />
          </motion.aside>
        )}
      </AnimatePresence>

      {/* Desktop Sidebar */}
      {isAuthenticated && (
        <motion.aside
          animate={{ width: isCollapsed ? 80 : 280 }}
          transition={{ type: "spring", damping: 30, stiffness: 300 }}
          className="hidden lg:flex fixed left-0 top-0 bottom-0 bg-white/80 backdrop-blur-md border-r border-gray-200/50 shadow-xl z-30 overflow-hidden"
        >
          <div className="w-full h-full overflow-hidden">
            <SidebarContent />
          </div>
        </motion.aside>
      )}
    </>
  );
}

export default Sidebar;

