import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { toast } from 'sonner';
import {
  BookOpen,
  Plus,
  Sparkles,
  ChevronLeft,
  Users,
  ClipboardList,
  X,
  Edit,
  Trash2,
  Send,
  GraduationCap,
  CheckCircle,
  Eye,
  MessageSquare
} from 'lucide-react';

import { examsAPI, homeworkAPI, subjectsAPI, classroomsAPI } from '../services/api';
import { Button } from '../components/ui/Button';
import { Card, CardHeader, CardTitle, CardContent } from '../components/ui/Card';

const DIFFICULTY_OPTIONS = [
  { value: 'EASY', label: 'Kolay', badge: 'bg-green-100 text-green-700' },
  { value: 'MEDIUM', label: 'Orta', badge: 'bg-yellow-100 text-yellow-700' },
  { value: 'HARD', label: 'Zor', badge: 'bg-red-100 text-red-700' }
];

// Tab Component
function TabButton({ active, onClick, icon: Icon, label, count }) {
  return (
    <button
      onClick={onClick}
      className={`flex items-center space-x-2 px-4 py-2 rounded-lg font-medium transition-all ${
        active
          ? 'bg-indigo-600 text-white shadow-lg'
          : 'bg-white text-gray-600 hover:bg-gray-50'
      }`}
    >
      <Icon className="h-4 w-4" />
      <span>{label}</span>
      {count !== undefined && (
        <span className={`px-2 py-0.5 rounded-full text-xs ${
          active ? 'bg-white/20 text-white' : 'bg-gray-100 text-gray-600'
        }`}>
          {count}
        </span>
      )}
    </button>
  );
}

// Exam Generator Form
function ExamGeneratorForm({ onExamGenerated }) {
  const [formData, setFormData] = useState({
    subject: '',
    grade: '',
    topic: '',
    questionCount: 5,
    questionType: 'mixed',
    difficulty: 'MEDIUM'
  });
  const [subjects, setSubjects] = useState([]);
  const [topics, setTopics] = useState([]);
  const [isGenerating, setIsGenerating] = useState(false);

  useEffect(() => {
    loadSubjects();
  }, []);

  useEffect(() => {
    if (formData.subject && formData.grade) {
      loadTopics();
    }
  }, [formData.subject, formData.grade]);

  const loadSubjects = async () => {
    try {
      const response = await subjectsAPI.getSubjectNames();
      setSubjects(response.data || []);
    } catch (error) {
      console.error('Error loading subjects:', error);
    }
  };

  const loadTopics = async () => {
    try {
      const response = await subjectsAPI.getAll(formData.grade, formData.subject);
      const uniqueTopics = [...new Set(response.data?.map(s => s.topic).filter(Boolean))];
      setTopics(uniqueTopics);
    } catch (error) {
      console.error('Error loading topics:', error);
    }
  };

  const handleGenerate = async () => {
    if (!formData.subject || !formData.grade) {
      toast.error('Ltfen ders ve snf sein');
      return;
    }

    setIsGenerating(true);
    try {
      const response = await examsAPI.generate({
        subject: formData.subject,
        grade: parseInt(formData.grade),
        topic: formData.topic || undefined,
        questionCount: formData.questionCount,
        questionType: formData.questionType
      });

      toast.success('Snav sorular oluturuldu!');
      onExamGenerated(response.data, formData);
    } catch (error) {
      toast.error(error.message || 'Snav oluturulamad');
    } finally {
      setIsGenerating(false);
    }
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center space-x-2">
          <Sparkles className="h-5 w-5 text-yellow-500" />
          <span>AI ile Snav Olutur</span>
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Ders</label>
            <select
              value={formData.subject}
              onChange={(e) => setFormData({ ...formData, subject: e.target.value, topic: '' })}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
            >
              <option value="">Ders Sein</option>
              {subjects.map(subject => (
                <option key={subject} value={subject}>{subject}</option>
              ))}
            </select>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Snf</label>
            <select
              value={formData.grade}
              onChange={(e) => setFormData({ ...formData, grade: e.target.value, topic: '' })}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
            >
              <option value="">Snf Sein</option>
              {[1, 2, 3, 4, 5, 6, 7, 8].map(grade => (
                <option key={grade} value={grade}>{grade}. Snf</option>
              ))}
            </select>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Konu (Opsiyonel)</label>
            <select
              value={formData.topic}
              onChange={(e) => setFormData({ ...formData, topic: e.target.value })}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
              disabled={!formData.subject || !formData.grade}
            >
              <option value="">Tm Konular</option>
              {topics.map(topic => (
                <option key={topic} value={topic}>{topic}</option>
              ))}
            </select>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Soru Says</label>
            <input
              type="number"
              min="1"
              max="20"
              value={formData.questionCount}
              onChange={(e) => setFormData({ ...formData, questionCount: parseInt(e.target.value) || 5 })}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
            />
          </div>

          <div className="md:col-span-2">
            <label className="block text-sm font-medium text-gray-700 mb-1">Soru Tipi</label>
            <div className="flex space-x-4">
              {[
                { value: 'mixed', label: 'Karma' },
                { value: 'multiple_choice', label: 'oktan Semeli' },
                { value: 'open_ended', label: 'Ak Ulu' }
              ].map(type => (
                <label key={type.value} className="flex items-center space-x-2 cursor-pointer">
                  <input
                    type="radio"
                    name="questionType"
                    value={type.value}
                    checked={formData.questionType === type.value}
                    onChange={(e) => setFormData({ ...formData, questionType: e.target.value })}
                    className="text-indigo-600 focus:ring-indigo-500"
                  />
                  <span className="text-sm text-gray-700">{type.label}</span>
                </label>
              ))}
            </div>
          </div>

          <div className="md:col-span-2">
            <label className="block text-sm font-medium text-gray-700 mb-1">Zorluk Seviyesi</label>
            <div className="flex space-x-4">
              {DIFFICULTY_OPTIONS.map(option => (
                <label key={option.value} className="flex items-center space-x-2 cursor-pointer">
                  <input
                    type="radio"
                    name="difficulty"
                    value={option.value}
                    checked={formData.difficulty === option.value}
                    onChange={(e) => setFormData({ ...formData, difficulty: e.target.value })}
                    className="text-indigo-600 focus:ring-indigo-500"
                  />
                  <span className="text-sm text-gray-700">{option.label}</span>
                </label>
              ))}
            </div>
          </div>
        </div>

        <Button
          onClick={handleGenerate}
          disabled={isGenerating || !formData.subject || !formData.grade}
          className="w-full"
        >
          {isGenerating ? (
            <>
              <div className="animate-spin rounded-full h-4 w-4 border-2 border-white border-t-transparent mr-2" />
              Oluturuluyor...
            </>
          ) : (
            <>
              <Sparkles className="h-4 w-4 mr-2" />
              Snav Olutur
            </>
          )}
        </Button>
      </CardContent>
    </Card>
  );
}

// Question Preview Component
function QuestionPreview({ question, index, onEdit, onDelete }) {
  const isMultipleChoice = question.type === 'multiple_choice';

  return (
    <motion.div
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      className="bg-white border border-gray-200 rounded-lg p-4"
    >
      <div className="flex justify-between items-start mb-2">
        <div className="flex items-center space-x-2">
          <span className="bg-indigo-100 text-indigo-700 px-2 py-1 rounded text-sm font-medium">
            Soru {index + 1}
          </span>
          <span className={`px-2 py-1 rounded text-xs ${
            isMultipleChoice ? 'bg-blue-100 text-blue-700' : 'bg-green-100 text-green-700'
          }`}>
            {isMultipleChoice ? 'oktan Semeli' : 'Ak Ulu'}
          </span>
          <span className="text-sm text-gray-500">{question.points} puan</span>
        </div>
        <div className="flex space-x-2">
          <button
            onClick={() => onEdit(index)}
            className="p-1 text-gray-400 hover:text-indigo-600 transition-colors"
          >
            <Edit className="h-4 w-4" />
          </button>
          <button
            onClick={() => onDelete(index)}
            className="p-1 text-gray-400 hover:text-red-600 transition-colors"
          >
            <Trash2 className="h-4 w-4" />
          </button>
        </div>
      </div>

      <p className="text-gray-800 mb-3">{question.question}</p>

      {isMultipleChoice && question.options && (
        <div className="space-y-1 ml-4">
          {question.options.map((option, i) => (
            <div
              key={i}
              className={`text-sm ${
                option.startsWith(question.correctAnswer + ')') || option.charAt(0) === question.correctAnswer
                  ? 'text-green-600 font-medium'
                  : 'text-gray-600'
              }`}
            >
              {option}
            </div>
          ))}
        </div>
      )}

      {!isMultipleChoice && question.expectedAnswer && (
        <div className="mt-2 p-2 bg-gray-50 rounded text-sm text-gray-600">
          <strong>Beklenen Cevap:</strong> {question.expectedAnswer}
        </div>
      )}
    </motion.div>
  );
}

// Exam Preview & Save Component
function ExamPreview({ examData, formData, onSave, onBack }) {
  const [title, setTitle] = useState(examData?.title || '');
  const [questions, setQuestions] = useState(examData?.questions || []);
  const [duration, setDuration] = useState(30);
  const [difficulty, setDifficulty] = useState(formData?.difficulty || 'MEDIUM');
  const [isSaving, setIsSaving] = useState(false);

  useEffect(() => {
    setDifficulty(formData?.difficulty || 'MEDIUM');
  }, [formData]);

  const totalPoints = questions.reduce((sum, q) => sum + (q.points || 0), 0);
  const selectedDifficulty = DIFFICULTY_OPTIONS.find(opt => opt.value === difficulty) || DIFFICULTY_OPTIONS[1];

  const handleDeleteQuestion = (index) => {
    setQuestions(questions.filter((_, i) => i !== index));
  };

  const handleSave = async () => {
    if (!title.trim()) {
      toast.error('Ltfen snav bal girin');
      return;
    }
    if (questions.length === 0) {
      toast.error('En az bir soru gerekli');
      return;
    }

    setIsSaving(true);
    try {
      await onSave({
        title,
        description: examData?.description,
        grade: formData.grade,
        subjectName: formData.subject,
        topic: formData.topic,
        questions,
        duration,
        difficulty
      });
    } finally {
      setIsSaving(false);
    }
  };

  return (
    <div className="space-y-4">
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle className="flex items-center space-x-2">
              <BookOpen className="h-5 w-5 text-indigo-600" />
              <span>Snav nizleme</span>
            </CardTitle>
            <Button variant="outline" onClick={onBack}>
              <ChevronLeft className="h-4 w-4 mr-1" />
              Geri
            </Button>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Snav Bal</label>
              <input
                type="text"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                placeholder="rn: 3. Snf Matematik Deerlendirme"
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Sre (dakika)</label>
              <input
                type="number"
                value={duration}
                onChange={(e) => setDuration(parseInt(e.target.value) || 30)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
              />
            </div>
          </div>

          <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
            <span className="text-sm text-gray-600">
              {questions.length} Soru  {formData.subject}  {formData.grade}. Snf
            </span>
            <div className="flex items-center gap-3">
              <span className="font-medium text-indigo-600">Toplam: {totalPoints} Puan</span>
              <span className={`px-2 py-0.5 rounded text-xs font-semibold ${selectedDifficulty.badge}`}>
                {selectedDifficulty.label}
              </span>
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Zorluk Seviyesi</label>
            <div className="flex flex-wrap gap-4">
              {DIFFICULTY_OPTIONS.map(option => (
                <label key={option.value} className="flex items-center space-x-2 cursor-pointer">
                  <input
                    type="radio"
                    name="previewDifficulty"
                    value={option.value}
                    checked={difficulty === option.value}
                    onChange={(e) => setDifficulty(e.target.value)}
                    className="text-indigo-600 focus:ring-indigo-500"
                  />
                  <span className="text-sm text-gray-700">{option.label}</span>
                </label>
              ))}
            </div>
          </div>
        </CardContent>
      </Card>

      <div className="space-y-3">
        {questions.map((question, index) => (
          <QuestionPreview
            key={index}
            question={question}
            index={index}
            onEdit={() => {/* TODO: Implement edit */}}
            onDelete={handleDeleteQuestion}
          />
        ))}
      </div>

      <div className="flex justify-end space-x-3">
        <Button
          onClick={handleSave}
          disabled={isSaving}
          className="bg-green-600 hover:bg-green-700"
        >
          {isSaving ? 'Kaydediliyor...' : 'Snav Kaydet'}
        </Button>
      </div>
    </div>
  );
}

// Assignment Modal
function AssignmentModal({ exam, onClose, onAssign }) {
  const [students, setStudents] = useState([]);
  const [classrooms, setClassrooms] = useState([]);
  const [selectedStudents, setSelectedStudents] = useState([]);
  const [selectedClassroom, setSelectedClassroom] = useState('');
  const [assignmentType, setAssignmentType] = useState('students'); // 'students' or 'classroom'
  const [dueDate, setDueDate] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [isAssigning, setIsAssigning] = useState(false);

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    try {
      const [studentsRes, classroomsRes] = await Promise.all([
        homeworkAPI.getStudents(),
        classroomsAPI.getAll()
      ]);
      setStudents(studentsRes.data || []);
      setClassrooms(classroomsRes.data || []);
    } catch (error) {
      toast.error('Veriler yklenemedi');
    } finally {
      setIsLoading(false);
    }
  };

  const toggleStudent = (studentId) => {
    setSelectedStudents(prev =>
      prev.includes(studentId)
        ? prev.filter(id => id !== studentId)
        : [...prev, studentId]
    );
  };

  const selectAll = () => {
    setSelectedStudents(students.map(s => s.id));
  };

  const handleAssign = async () => {
    if (assignmentType === 'students' && selectedStudents.length === 0) {
      toast.error('En az bir renci sein');
      return;
    }

    if (assignmentType === 'classroom' && !selectedClassroom) {
      toast.error('Bir snf sein');
      return;
    }

    setIsAssigning(true);
    try {
      if (assignmentType === 'classroom') {
        await homeworkAPI.assign(exam.id, null, dueDate || undefined, parseInt(selectedClassroom));
        const classroom = classrooms.find(c => c.id === parseInt(selectedClassroom));
        toast.success(`${classroom?.name || 'Snf'} rencilerine dev atand`);
      } else {
        await homeworkAPI.assign(exam.id, selectedStudents, dueDate || undefined);
        toast.success(`${selectedStudents.length} renciye dev atand`);
      }
      onAssign();
      onClose();
    } catch (error) {
      toast.error(error.message || 'Atama baarsz');
    } finally {
      setIsAssigning(false);
    }
  };

  const selectedClassroomData = classrooms.find(c => c.id === parseInt(selectedClassroom));

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
      <motion.div
        initial={{ opacity: 0, scale: 0.95 }}
        animate={{ opacity: 1, scale: 1 }}
        className="bg-white rounded-xl shadow-xl w-full max-w-md max-h-[90vh] overflow-hidden"
      >
        <div className="p-4 border-b flex items-center justify-between">
          <h3 className="text-lg font-semibold">rencilere Ata</h3>
          <button onClick={onClose} className="p-1 hover:bg-gray-100 rounded">
            <X className="h-5 w-5" />
          </button>
        </div>

        <div className="p-4 space-y-4 overflow-y-auto max-h-[calc(90vh-130px)]">
          <div className="p-3 bg-indigo-50 rounded-lg">
            <p className="font-medium text-indigo-900">{exam.title}</p>
            <p className="text-sm text-indigo-700">
              {exam.subjectName}  {exam.grade}. Snf
            </p>
          </div>

          {/* Assignment Type Toggle */}
          <div className="flex rounded-lg border border-gray-200 p-1">
            <button
              onClick={() => setAssignmentType('students')}
              className={`flex-1 py-2 px-3 rounded-md text-sm font-medium transition-colors ${
                assignmentType === 'students'
                  ? 'bg-indigo-600 text-white'
                  : 'text-gray-600 hover:bg-gray-50'
              }`}
            >
              <Users className="h-4 w-4 inline mr-1" />
              renci Se
            </button>
            <button
              onClick={() => setAssignmentType('classroom')}
              className={`flex-1 py-2 px-3 rounded-md text-sm font-medium transition-colors ${
                assignmentType === 'classroom'
                  ? 'bg-indigo-600 text-white'
                  : 'text-gray-600 hover:bg-gray-50'
              }`}
            >
              <GraduationCap className="h-4 w-4 inline mr-1" />
              Snfa Ata
            </button>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Son Teslim Tarihi (Opsiyonel)
            </label>
            <input
              type="datetime-local"
              value={dueDate}
              onChange={(e) => setDueDate(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
            />
          </div>

          {isLoading ? (
            <div className="text-center py-4 text-gray-500">Ykleniyor...</div>
          ) : assignmentType === 'classroom' ? (
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Snf Sein
              </label>
              {classrooms.length === 0 ? (
                <div className="text-center py-4 text-gray-500 bg-gray-50 rounded-lg">
                  <p>Henz snf oluturmadnz</p>
                  <p className="text-sm mt-1">Snflarm sayfasndan snf oluturabilirsiniz</p>
                </div>
              ) : (
                <>
                  <select
                    value={selectedClassroom}
                    onChange={(e) => setSelectedClassroom(e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
                  >
                    <option value="">Snf sein...</option>
                    {classrooms.map(classroom => (
                      <option key={classroom.id} value={classroom.id}>
                        {classroom.name} ({classroom.grade}. Snf) - {classroom._count?.students || 0} renci
                      </option>
                    ))}
                  </select>
                  {selectedClassroomData && (
                    <div className="mt-3 p-3 bg-green-50 rounded-lg">
                      <p className="text-sm text-green-800">
                        <strong>{selectedClassroomData.name}</strong> snfndaki{' '}
                        <strong>{selectedClassroomData._count?.students || 0}</strong> renciye atanacak
                      </p>
                    </div>
                  )}
                </>
              )}
            </div>
          ) : (
            <div>
              <div className="flex items-center justify-between mb-2">
                <label className="block text-sm font-medium text-gray-700">
                  renciler ({selectedStudents.length}/{students.length})
                </label>
                <button
                  onClick={selectAll}
                  className="text-sm text-indigo-600 hover:underline"
                >
                  Tmn Se
                </button>
              </div>

              {students.length === 0 ? (
                <div className="text-center py-4 text-gray-500">renci bulunamad</div>
              ) : (
                <div className="max-h-48 overflow-y-auto border rounded-lg divide-y">
                  {students.map(student => (
                    <label
                      key={student.id}
                      className="flex items-center p-3 hover:bg-gray-50 cursor-pointer"
                    >
                      <input
                        type="checkbox"
                        checked={selectedStudents.includes(student.id)}
                        onChange={() => toggleStudent(student.id)}
                        className="h-4 w-4 text-indigo-600 rounded"
                      />
                      <div className="ml-3">
                        <p className="text-sm font-medium text-gray-900">{student.name}</p>
                        <p className="text-xs text-gray-500">{student.email}</p>
                      </div>
                    </label>
                  ))}
                </div>
              )}
            </div>
          )}
        </div>

        <div className="p-4 border-t flex justify-end space-x-3">
          <Button variant="outline" onClick={onClose}>ptal</Button>
          <Button
            onClick={handleAssign}
            disabled={isAssigning || (assignmentType === 'students' ? selectedStudents.length === 0 : !selectedClassroom)}
          >
            {isAssigning ? 'Atanyor...' : (
              <>
                <Send className="h-4 w-4 mr-1" />
                {assignmentType === 'classroom'
                  ? `Snfa Ata`
                  : `Ata (${selectedStudents.length})`}
              </>
            )}
          </Button>
        </div>
      </motion.div>
    </div>
  );
}

// Exams List Component
function ExamsList({ exams, onRefresh }) {
  const [assigningExam, setAssigningExam] = useState(null);

  const handleDelete = async (examId) => {
    if (!window.confirm('Bu snav silmek istediinize emin misiniz?')) return;

    try {
      await examsAPI.delete(examId);
      toast.success('Snav silindi');
      onRefresh();
    } catch (error) {
      toast.error(error.message || 'Silme baarsz');
    }
  };

  if (exams.length === 0) {
    return (
      <div className="text-center py-12 bg-white rounded-lg border">
        <BookOpen className="h-12 w-12 text-gray-300 mx-auto mb-4" />
        <p className="text-gray-500">Henz snav oluturmadnz</p>
      </div>
    );
  }

  return (
    <>
      <div className="grid gap-4">
        {exams.map(exam => {
          const difficultyMeta = DIFFICULTY_OPTIONS.find(opt => opt.value === exam.difficulty);
          return (
          <motion.div
            key={exam.id}
             initial={{ opacity: 0, y: 10 }}
             animate={{ opacity: 1, y: 0 }}
             className="bg-white border rounded-lg p-4"
           >
             <div className="flex items-start justify-between">
               <div>
                 <h3 className="font-semibold text-gray-900">{exam.title}</h3>
                 <p className="text-sm text-gray-600">
                   {exam.subjectName}  {exam.grade}. Snf
                   {exam.topic && `  ${exam.topic}`}
                 </p>
                 <div className="flex items-center space-x-3 mt-2 text-sm">
                   <span className="text-gray-500">
                     {exam.questions?.length || 0} Soru
                   </span>
                   <span className="text-gray-500">
                     {exam.totalPoints} Puan
                   </span>
                   <span className={`px-2 py-0.5 rounded ${
                     exam.status === 'PUBLISHED' ? 'bg-green-100 text-green-700' : 'bg-yellow-100 text-yellow-700'
                   }`}>
                     {exam.status === 'PUBLISHED' ? 'Yaynland' : 'Taslak'}
                   </span>
                  {difficultyMeta && (
                    <span className={`px-2 py-0.5 rounded text-xs font-semibold ${difficultyMeta.badge}`}>
                      {difficultyMeta.label}
                    </span>
                  )}
                   {exam.assignedCount > 0 && (
                     <span className="text-indigo-600">
                       {exam.assignedCount} renciye atand
                     </span>
                   )}
                 </div>
               </div>
               <div className="flex space-x-2">
                 <Button
                   variant="outline"
                   size="sm"
                   onClick={() => setAssigningExam(exam)}
                 >
                   <Users className="h-4 w-4 mr-1" />
                   Ata
                 </Button>
                 <button
                   onClick={() => handleDelete(exam.id)}
                   className="p-2 text-gray-400 hover:text-red-600 hover:bg-red-50 rounded transition-colors"
                 >
                   <Trash2 className="h-4 w-4" />
                 </button>
               </div>
             </div>
           </motion.div>
        );})}
       </div>

      {assigningExam && (
        <AssignmentModal
          exam={assigningExam}
          onClose={() => setAssigningExam(null)}
          onAssign={onRefresh}
        />
      )}
    </>
  );
}

// Grading Modal Component - retmen ak ulu sorular deerlendirir
function GradingModal({ homework, onClose, onGraded }) {
  const [examDetails, setExamDetails] = useState(null);
  const [grades, setGrades] = useState([]);
  const [feedback, setFeedback] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    loadHomeworkDetails();
  }, [homework.id]);

  const loadHomeworkDetails = async () => {
    try {
      const response = await homeworkAPI.getById(homework.id);
      const data = response.data;
      setExamDetails(data);

      // Initialize grades from studentAnswers
      const questions = data.exam?.questions || [];
      const studentAnswers = data.studentAnswers || [];

      const initialGrades = questions.map((q, index) => {
        const answer = studentAnswers.find(a => a.questionId === q.id) || studentAnswers[index];
        return {
          questionId: q.id || index,
          points: answer?.points ?? (q.type === 'open_ended' ? 0 : (answer?.isCorrect ? q.points : 0)),
          comment: answer?.teacherComment || ''
        };
      });
      setGrades(initialGrades);
    } catch (error) {
      toast.error('dev detaylar yklenemedi');
      onClose();
    } finally {
      setIsLoading(false);
    }
  };

  const updateGrade = (questionId, field, value) => {
    setGrades(prev => prev.map(g =>
      g.questionId === questionId ? { ...g, [field]: value } : g
    ));
  };

  const getTotalScore = () => {
    return grades.reduce((sum, g) => sum + (parseFloat(g.points) || 0), 0);
  };

  const handleSubmit = async () => {
    setIsSubmitting(true);
    try {
      await homeworkAPI.grade(homework.id, grades, feedback);
      toast.success('dev deerlendirildi!');
      onGraded();
      onClose();
    } catch (error) {
      toast.error(error.message || 'Deerlendirme baarsz');
    } finally {
      setIsSubmitting(false);
    }
  };

  if (isLoading) {
    return (
      <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
        <div className="bg-white rounded-xl p-8">
          <div className="animate-spin h-8 w-8 border-4 border-indigo-600 border-t-transparent rounded-full mx-auto" />
          <p className="mt-4 text-gray-600">Ykleniyor...</p>
        </div>
      </div>
    );
  }

  const questions = examDetails?.exam?.questions || [];
  const studentAnswers = examDetails?.studentAnswers || [];

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
      <motion.div
        initial={{ opacity: 0, scale: 0.95 }}
        animate={{ opacity: 1, scale: 1 }}
        className="bg-white rounded-xl shadow-xl w-full max-w-3xl max-h-[90vh] overflow-hidden flex flex-col"
      >
        {/* Header */}
        <div className="p-4 border-b flex items-center justify-between bg-gradient-to-r from-indigo-500 to-purple-600">
          <div className="text-white">
            <h3 className="text-lg font-semibold">dev Deerlendirme</h3>
            <p className="text-sm text-white/80">
              {homework.student?.name} - {homework.exam?.title}
            </p>
          </div>
          <button onClick={onClose} className="p-1 hover:bg-white/20 rounded text-white">
            <X className="h-5 w-5" />
          </button>
        </div>

        {/* Content */}
        <div className="flex-1 overflow-y-auto p-4 space-y-4">
          {questions.map((question, index) => {
            const studentAnswer = studentAnswers.find(a => a.questionId === question.id) || studentAnswers[index];
            const grade = grades.find(g => g.questionId === (question.id || index)) || { points: 0, comment: '' };
            const isOpenEnded = question.type === 'open_ended';

            return (
              <div key={question.id || index} className="bg-gray-50 rounded-lg p-4 border">
                {/* Question Header */}
                <div className="flex items-start justify-between mb-3">
                  <div className="flex items-center gap-2">
                    <span className="bg-indigo-100 text-indigo-700 px-2 py-1 rounded text-sm font-medium">
                      Soru {index + 1}
                    </span>
                    <span className={`px-2 py-1 rounded text-xs ${
                      isOpenEnded ? 'bg-green-100 text-green-700' : 'bg-blue-100 text-blue-700'
                    }`}>
                      {isOpenEnded ? 'Ak Ulu' : 'oktan Semeli'}
                    </span>
                    <span className="text-sm text-gray-500">
                      Maks: {question.points} puan
                    </span>
                  </div>
                </div>

                {/* Question Text */}
                <p className="text-gray-800 font-medium mb-3">{question.question}</p>

                {/* Multiple Choice Options */}
                {!isOpenEnded && question.options && (
                  <div className="space-y-1 mb-3 ml-4 text-sm">
                    {question.options.map((option, i) => {
                      const optionLetter = option.charAt(0);
                      const isCorrectOption = optionLetter === question.correctAnswer;
                      const isStudentChoice = studentAnswer?.answer === optionLetter;

                      return (
                        <div
                          key={i}
                          className={`p-2 rounded ${
                            isCorrectOption 
                              ? 'bg-green-100 text-green-800 font-medium' 
                              : isStudentChoice 
                                ? 'bg-red-100 text-red-800' 
                                : 'text-gray-600'
                          }`}
                        >
                          {isStudentChoice && !isCorrectOption && ' '}
                          {isCorrectOption && ' '}
                          {option}
                        </div>
                      );
                    })}
                  </div>
                )}

                {/* Student's Answer */}
                <div className="bg-white border rounded-lg p-3 mb-3">
                  <p className="text-xs text-gray-500 mb-1">renci Cevab:</p>
                  <p className={`${studentAnswer?.answer ? 'text-gray-800' : 'text-gray-400 italic'}`}>
                    {studentAnswer?.answer || 'Cevap verilmedi'}
                  </p>
                </div>

                {/* Expected Answer for Open-Ended */}
                {isOpenEnded && question.expectedAnswer && (
                  <div className="bg-blue-50 border border-blue-200 rounded-lg p-3 mb-3">
                    <p className="text-xs text-blue-600 mb-1">Beklenen Cevap:</p>
                    <p className="text-blue-800 text-sm">{question.expectedAnswer}</p>
                  </div>
                )}

                {/* Grading Section */}
                <div className="flex items-center gap-4 pt-3 border-t">
                  <div className="flex items-center gap-2">
                    <label className="text-sm font-medium text-gray-700">Puan:</label>
                    <input
                      type="number"
                      min="0"
                      max={question.points}
                      value={grade.points}
                      onChange={(e) => updateGrade(question.id || index, 'points', parseFloat(e.target.value) || 0)}
                      className="w-20 px-2 py-1 border rounded-lg focus:ring-2 focus:ring-indigo-500 text-center"
                      disabled={!isOpenEnded && studentAnswer?.isCorrect !== undefined}
                    />
                    <span className="text-sm text-gray-500">/ {question.points}</span>
                  </div>

                  {isOpenEnded && (
                    <div className="flex-1">
                      <input
                        type="text"
                        placeholder="Yorum ekle (opsiyonel)"
                        value={grade.comment}
                        onChange={(e) => updateGrade(question.id || index, 'comment', e.target.value)}
                        className="w-full px-3 py-1 border rounded-lg focus:ring-2 focus:ring-indigo-500 text-sm"
                      />
                    </div>
                  )}
                </div>
              </div>
            );
          })}

          {/* General Feedback */}
          <div className="bg-white border rounded-lg p-4">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              <MessageSquare className="h-4 w-4 inline mr-1" />
              Genel Deerlendirme (Opsiyonel)
            </label>
            <textarea
              value={feedback}
              onChange={(e) => setFeedback(e.target.value)}
              rows={3}
              placeholder="renciye genel bir geri bildirim yazn..."
              className="w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-indigo-500"
            />
          </div>
        </div>

        {/* Footer */}
        <div className="p-4 border-t bg-gray-50 flex items-center justify-between">
          <div className="text-lg font-semibold text-gray-900">
            Toplam Puan: <span className="text-indigo-600">{getTotalScore()}</span>
            <span className="text-gray-500"> / {examDetails?.exam?.totalPoints || 0}</span>
          </div>
          <div className="flex space-x-3">
            <Button variant="outline" onClick={onClose}>ptal</Button>
            <Button onClick={handleSubmit} disabled={isSubmitting}>
              {isSubmitting ? 'Kaydediliyor...' : (
                <>
                  <CheckCircle className="h-4 w-4 mr-1" />
                  Deerlendirmeyi Kaydet
                </>
              )}
            </Button>
          </div>
        </div>
      </motion.div>
    </div>
  );
}

// Homework List Component (Teacher View)
function HomeworkList({ homeworks, onRefresh }) {
  const [gradingHomework, setGradingHomework] = useState(null);

  const statusColors = {
    ASSIGNED: 'bg-yellow-100 text-yellow-700',
    SUBMITTED: 'bg-blue-100 text-blue-700',
    GRADED: 'bg-green-100 text-green-700'
  };

  const statusLabels = {
    ASSIGNED: 'Bekliyor',
    SUBMITTED: 'Gnderildi',
    GRADED: 'Puanland'
  };

  if (homeworks.length === 0) {
    return (
      <div className="text-center py-12 bg-white rounded-lg border">
        <ClipboardList className="h-12 w-12 text-gray-300 mx-auto mb-4" />
        <p className="text-gray-500">Henz dev atamas yaplmad</p>
      </div>
    );
  }

  return (
    <>
      <div className="bg-white rounded-lg border overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">renci</th>
              <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Snav</th>
              <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Durum</th>
              <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Puan</th>
              <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Tarih</th>
              <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">lem</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-200">
            {homeworks.map(hw => (
              <tr key={hw.id} className="hover:bg-gray-50">
                <td className="px-4 py-3">
                  <div>
                    <p className="font-medium text-gray-900">{hw.student?.name}</p>
                    <p className="text-sm text-gray-500">{hw.student?.email}</p>
                  </div>
                </td>
                <td className="px-4 py-3">
                  <p className="text-gray-900">{hw.exam?.title}</p>
                  <p className="text-sm text-gray-500">{hw.exam?.subjectName}</p>
                </td>
                <td className="px-4 py-3">
                  <span className={`px-2 py-1 rounded text-xs font-medium ${statusColors[hw.status]}`}>
                    {statusLabels[hw.status]}
                  </span>
                </td>
                <td className="px-4 py-3">
                  {hw.score !== null ? (
                    <span className="font-medium">{hw.score}/{hw.exam?.totalPoints}</span>
                  ) : (
                    <span className="text-gray-400">-</span>
                  )}
                </td>
                <td className="px-4 py-3 text-sm text-gray-500">
                  {hw.submittedAt ? new Date(hw.submittedAt).toLocaleDateString('tr-TR') : '-'}
                </td>
                <td className="px-4 py-3">
                  {hw.status === 'SUBMITTED' ? (
                    <Button
                      size="sm"
                      onClick={() => setGradingHomework(hw)}
                      className="bg-indigo-600 hover:bg-indigo-700"
                    >
                      <CheckCircle className="h-4 w-4 mr-1" />
                      Deerlendir
                    </Button>
                  ) : hw.status === 'GRADED' ? (
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => setGradingHomework(hw)}
                    >
                      <Eye className="h-4 w-4 mr-1" />
                      Grntle
                    </Button>
                  ) : (
                    <span className="text-gray-400 text-sm">Bekleniyor</span>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Grading Modal */}
      {gradingHomework && (
        <GradingModal
          homework={gradingHomework}
          onClose={() => setGradingHomework(null)}
          onGraded={onRefresh}
        />
      )}
    </>
  );
}

// Main Teacher Page Component
function TeacherPage() {
  const [activeTab, setActiveTab] = useState('create');
  const [exams, setExams] = useState([]);
  const [homeworks, setHomeworks] = useState([]);
  const [generatedExam, setGeneratedExam] = useState(null);
  const [generatorFormData, setGeneratorFormData] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (activeTab === 'exams') {
      loadExams();
    } else if (activeTab === 'assignments') {
      loadHomeworks();
    }
  }, [activeTab]);

  const loadExams = async () => {
    setIsLoading(true);
    try {
      const response = await examsAPI.getAll();
      setExams(response.data || []);
    } catch (error) {
      toast.error('Snavlar yklenemedi');
    } finally {
      setIsLoading(false);
    }
  };

  const loadHomeworks = async () => {
    setIsLoading(true);
    try {
      const response = await homeworkAPI.getTeacherHomeworks();
      setHomeworks(response.data || []);
    } catch (error) {
      toast.error('devler yklenemedi');
    } finally {
      setIsLoading(false);
    }
  };

  const handleExamGenerated = (examData, formData) => {
    setGeneratedExam(examData);
    setGeneratorFormData(formData);
  };

  const handleSaveExam = async (examData) => {
    try {
      await examsAPI.create(examData);
      toast.success('Snav kaydedildi!');
      setGeneratedExam(null);
      setGeneratorFormData(null);
      setActiveTab('exams');
      loadExams();
    } catch (error) {
      toast.error(error.message || 'Kayt baarsz');
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
            <GraduationCap className="h-7 w-7 text-indigo-600" />
            retmen Paneli
          </h1>
          <p className="text-gray-600 mt-1">AI ile snav oluturun ve rencilere atayn</p>
        </div>
      </div>

      {/* Tabs */}
      <div className="flex space-x-2">
        <TabButton
          active={activeTab === 'create'}
          onClick={() => {
            setActiveTab('create');
            setGeneratedExam(null);
          }}
          icon={Plus}
          label="Snav Olutur"
        />
        <TabButton
          active={activeTab === 'exams'}
          onClick={() => setActiveTab('exams')}
          icon={BookOpen}
          label="Snavlarm"
          count={exams.length}
        />
        <TabButton
          active={activeTab === 'assignments'}
          onClick={() => setActiveTab('assignments')}
          icon={ClipboardList}
          label="dev Takibi"
          count={homeworks.filter(h => h.status === 'SUBMITTED').length}
        />
      </div>

      {/* Content */}
      <AnimatePresence mode="wait">
        {activeTab === 'create' && (
          <motion.div
            key="create"
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
          >
            {generatedExam ? (
              <ExamPreview
                examData={generatedExam}
                formData={generatorFormData}
                onSave={handleSaveExam}
                onBack={() => setGeneratedExam(null)}
              />
            ) : (
              <ExamGeneratorForm onExamGenerated={handleExamGenerated} />
            )}
          </motion.div>
        )}

        {activeTab === 'exams' && (
          <motion.div
            key="exams"
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
          >
            {isLoading ? (
              <div className="text-center py-12">
                <div className="animate-spin h-8 w-8 border-4 border-indigo-600 border-t-transparent rounded-full mx-auto" />
              </div>
            ) : (
              <ExamsList exams={exams} onRefresh={loadExams} />
            )}
          </motion.div>
        )}

        {activeTab === 'assignments' && (
          <motion.div
            key="assignments"
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
          >
            {isLoading ? (
              <div className="text-center py-12">
                <div className="animate-spin h-8 w-8 border-4 border-indigo-600 border-t-transparent rounded-full mx-auto" />
              </div>
            ) : (
              <HomeworkList homeworks={homeworks} onRefresh={loadHomeworks} />
            )}
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}

export default TeacherPage;

