import React, { useState } from 'react';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { motion, AnimatePresence } from 'framer-motion';
import {
  Users as UsersIcon,
  UserPlus,
  Edit,
  Trash2,
  Shield,
  Mail,
  Lock,
  X,
  Save,
  AlertCircle,
  Plus,
  FileText
} from 'lucide-react';
import { toast } from 'sonner';

import { usersAPI } from '../services/api';
import { Button } from '../components/ui/Button';
import { Input } from '../components/ui/Input';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../components/ui/Card';

const AdminPage = () => {
  const queryClient = useQueryClient();
  const [activeTab, setActiveTab] = useState('users'); // 'users' or 'roles'
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingUser, setEditingUser] = useState(null);
  const [editingRole, setEditingRole] = useState(null);
  const [selectedRoleFilter, setSelectedRoleFilter] = useState('ALL');
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    name: '',
    roles: []
  });
  const [roleFormData, setRoleFormData] = useState({
    name: '',
    description: ''
  });

  // Fetch users
  const { data: usersData, isLoading: loadingUsers } = useQuery({
    queryKey: ['users'],
    queryFn: usersAPI.getAll
  });

  // Fetch available roles
  const { data: rolesData, isLoading: loadingRoles } = useQuery({
    queryKey: ['roles'],
    queryFn: usersAPI.getRoles
  });

  const users = usersData?.users || [];
  const roles = rolesData?.roles || [];

  // Filter users by selected role
  const filteredUsers = selectedRoleFilter === 'ALL'
    ? users
    : users.filter(user => user.roles?.includes(selectedRoleFilter));

  // Create user mutation
  const createMutation = useMutation({
    mutationFn: usersAPI.create,
    onSuccess: () => {
      queryClient.invalidateQueries(['users']);
      toast.success('Kullanc baaryla oluturuldu');
      closeModal();
    },
    onError: (error) => {
      toast.error(error.message || 'Kullanc oluturulurken hata olutu');
    }
  });

  // Update user mutation
  const updateMutation = useMutation({
    mutationFn: ({ id, data }) => usersAPI.update(id, data),
    onSuccess: () => {
      queryClient.invalidateQueries(['users']);
      toast.success('Kullanc baaryla gncellendi');
      closeModal();
    },
    onError: (error) => {
      toast.error(error.message || 'Kullanc gncellenirken hata olutu');
    }
  });

  // Delete user mutation
  const deleteMutation = useMutation({
    mutationFn: usersAPI.delete,
    onSuccess: () => {
      queryClient.invalidateQueries(['users']);
      toast.success('Kullanc baaryla silindi');
    },
    onError: (error) => {
      toast.error(error.message || 'Kullanc silinirken hata olutu');
    }
  });

  // Create role mutation
  const createRoleMutation = useMutation({
    mutationFn: usersAPI.createRole,
    onSuccess: () => {
      queryClient.invalidateQueries(['roles']);
      toast.success('Rol baaryla oluturuldu');
      closeModal();
    },
    onError: (error) => {
      toast.error(error.message || 'Rol oluturulurken hata olutu');
    }
  });

  // Update role mutation
  const updateRoleMutation = useMutation({
    mutationFn: ({ id, data }) => usersAPI.updateRole(id, data),
    onSuccess: () => {
      queryClient.invalidateQueries(['roles']);
      queryClient.invalidateQueries(['users']);
      toast.success('Rol baaryla gncellendi');
      closeModal();
    },
    onError: (error) => {
      toast.error(error.message || 'Rol gncellenirken hata olutu');
    }
  });

  // Delete role mutation
  const deleteRoleMutation = useMutation({
    mutationFn: usersAPI.deleteRole,
    onSuccess: () => {
      queryClient.invalidateQueries(['roles']);
      queryClient.invalidateQueries(['users']);
      toast.success('Rol baaryla silindi');
    },
    onError: (error) => {
      toast.error(error.message || 'Rol silinirken hata olutu');
    }
  });

  const openCreateModal = () => {
    if (activeTab === 'users') {
      setEditingUser(null);
      setFormData({ email: '', password: '', name: '', roles: [] });
    } else {
      setEditingRole(null);
      setRoleFormData({ name: '', description: '' });
    }
    setIsModalOpen(true);
  };

  const openEditModal = (user) => {
    setEditingUser(user);
    setFormData({
      email: user.email,
      password: '',
      name: user.name || '',
      roles: user.roles || []
    });
    setIsModalOpen(true);
  };

  const openEditRoleModal = (role) => {
    setEditingRole(role);
    setRoleFormData({
      name: role.name,
      description: role.description || ''
    });
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setEditingUser(null);
    setEditingRole(null);
    setFormData({ email: '', password: '', name: '', roles: [] });
    setRoleFormData({ name: '', description: '' });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    // Validation
    if (!formData.email) {
      toast.error('Email gereklidir');
      return;
    }

    if (!editingUser && !formData.password) {
      toast.error('ifre gereklidir');
      return;
    }

    if (formData.password && formData.password.length < 6) {
      toast.error('ifre en az 6 karakter olmaldr');
      return;
    }

    if (editingUser) {
      // Update
      const updateData = {
        email: formData.email,
        name: formData.name,
        roles: formData.roles
      };
      if (formData.password) {
        updateData.password = formData.password;
      }
      updateMutation.mutate({ id: editingUser.id, data: updateData });
    } else {
      // Create
      createMutation.mutate(formData);
    }
  };

  const handleDelete = (user) => {
    if (window.confirm(`"${user.email}" kullancsn silmek istediinizden emin misiniz?`)) {
      deleteMutation.mutate(user.id);
    }
  };

  const handleSubmitRole = (e) => {
    e.preventDefault();

    // Validation
    if (!roleFormData.name) {
      toast.error('Rol ad gereklidir');
      return;
    }

    if (editingRole) {
      // Update
      updateRoleMutation.mutate({ id: editingRole.id, data: roleFormData });
    } else {
      // Create
      createRoleMutation.mutate(roleFormData);
    }
  };

  const handleDeleteRole = (role) => {
    if (window.confirm(`"${role.name}" roln silmek istediinizden emin misiniz?`)) {
      deleteRoleMutation.mutate(role.id);
    }
  };

  const toggleRole = (roleName) => {
    setFormData(prev => ({
      ...prev,
      roles: prev.roles.includes(roleName)
        ? prev.roles.filter(r => r !== roleName)
        : [...prev.roles, roleName]
    }));
  };

  const getRoleBadgeColor = (roleName) => {
    switch (roleName) {
      case 'ADMIN':
        return 'bg-red-100 text-red-800 border-red-200';
      case 'TEACHER':
        return 'bg-purple-100 text-purple-800 border-purple-200';
      case 'STUDENT':
        return 'bg-yellow-100 text-yellow-800 border-yellow-200';
      default:
        return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  if (loadingUsers || loadingRoles) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-indigo-600"></div>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8 max-w-7xl">
      {/* Header */}
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 flex items-center gap-3">
            <Shield className="h-8 w-8 text-indigo-600" />
            Ynetim Paneli
          </h1>
          <p className="text-gray-600 mt-2">
            Sistem kullanclarn ve rollerini ynetin
          </p>
        </div>
        <Button onClick={openCreateModal} className="flex items-center gap-2">
          {activeTab === 'users' ? (
            <>
              <UserPlus className="h-5 w-5" />
              Yeni Kullanc
            </>
          ) : (
            <>
              <Plus className="h-5 w-5" />
              Yeni Rol
            </>
          )}
        </Button>
      </div>

      {/* Tabs */}
      <div className="mb-6">
        <div className="border-b border-gray-200">
          <nav className="-mb-px flex space-x-8" aria-label="Tabs">
            <button
              onClick={() => setActiveTab('users')}
              className={`
                ${activeTab === 'users'
                  ? 'border-indigo-500 text-indigo-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'}
                whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm flex items-center gap-2 transition-colors
              `}
            >
              <UsersIcon className="h-5 w-5" />
              Kullanclar
            </button>
            <button
              onClick={() => setActiveTab('roles')}
              className={`
                ${activeTab === 'roles'
                  ? 'border-indigo-500 text-indigo-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'}
                whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm flex items-center gap-2 transition-colors
              `}
            >
              <Shield className="h-5 w-5" />
              Roller
            </button>
          </nav>
        </div>
      </div>

      {/* Stats Cards */}
      {activeTab === 'users' && (
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Toplam Kullanc</p>
                <p className="text-2xl font-bold text-gray-900">{users.length}</p>
              </div>
              <UsersIcon className="h-8 w-8 text-indigo-600 opacity-75" />
            </div>
          </CardContent>
        </Card>

        {roles.map((role) => (
          <Card key={role.id}>
            <CardContent className="pt-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600">{role.name}</p>
                  <p className="text-2xl font-bold text-gray-900">
                    {users.filter(u => u.roles?.includes(role.name)).length}
                  </p>
                </div>
                <Shield className={`h-8 w-8 opacity-75 ${
                  role.name === 'ADMIN' ? 'text-red-600' :
                  role.name === 'TEACHER' ? 'text-purple-600' :
                  role.name === 'STUDENT' ? 'text-yellow-600' :
                  'text-gray-600'
                }`} />
              </div>
            </CardContent>
          </Card>
        ))}
      </div>
      )}

      {/* Users Table */}
      {activeTab === 'users' && (
      <Card>
        <CardHeader>
          <div className="flex justify-between items-center">
            <div>
              <CardTitle>Kullanclar</CardTitle>
              <CardDescription>
                {selectedRoleFilter === 'ALL'
                  ? 'Sistemdeki tm kullanclar ve rolleri'
                  : `${selectedRoleFilter} rolne sahip kullanclar`}
              </CardDescription>
            </div>
            {/* Role Filter Dropdown */}
            <div className="flex items-center gap-2">
              <label className="text-sm font-medium text-gray-700">Filtre:</label>
              <select
                value={selectedRoleFilter}
                onChange={(e) => setSelectedRoleFilter(e.target.value)}
                className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 text-sm"
              >
                <option value="ALL">Tm Kullanclar</option>
                {roles.map((role) => (
                  <option key={role.id} value={role.name}>
                    {role.name}
                  </option>
                ))}
              </select>
              {selectedRoleFilter !== 'ALL' && (
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => setSelectedRoleFilter('ALL')}
                  className="text-gray-600 hover:text-gray-900"
                >
                  <X className="h-4 w-4" />
                </Button>
              )}
            </div>
          </div>
        </CardHeader>
        <CardContent>
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Kullanc
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Email
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Roller
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Oluturulma
                  </th>
                  <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                    lemler
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredUsers.map((user) => (
                  <tr key={user.id} className="hover:bg-gray-50 transition-colors">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <div className="flex-shrink-0 h-10 w-10 bg-indigo-100 rounded-full flex items-center justify-center">
                          <span className="text-indigo-600 font-semibold">
                            {user.name?.charAt(0) || user.email.charAt(0).toUpperCase()}
                          </span>
                        </div>
                        <div className="ml-4">
                          <div className="text-sm font-medium text-gray-900">
                            {user.name || 'simsiz'}
                          </div>
                          <div className="text-sm text-gray-500">ID: {user.id}</div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-900">{user.email}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex gap-2 flex-wrap">
                        {user.roles?.map((role) => (
                          <span
                            key={role}
                            className={`px-2 py-1 text-xs font-semibold rounded-full border ${getRoleBadgeColor(role)}`}
                          >
                            {role}
                          </span>
                        ))}
                        {(!user.roles || user.roles.length === 0) && (
                          <span className="px-2 py-1 text-xs text-gray-500 italic">
                            Rol yok
                          </span>
                        )}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {new Date(user.createdAt).toLocaleDateString('tr-TR')}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                      <div className="flex justify-end gap-2">
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => openEditModal(user)}
                          className="text-indigo-600 hover:text-indigo-900"
                        >
                          <Edit className="h-4 w-4" />
                        </Button>
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => handleDelete(user)}
                          className="text-red-600 hover:text-red-900"
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {filteredUsers.length === 0 && (
            <div className="text-center py-12">
              <UsersIcon className="mx-auto h-12 w-12 text-gray-400" />
              <h3 className="mt-2 text-sm font-medium text-gray-900">
                {selectedRoleFilter === 'ALL' ? 'Kullanc bulunamad' : `${selectedRoleFilter} rolne sahip kullanc bulunamad`}
              </h3>
              <p className="mt-1 text-sm text-gray-500">
                {selectedRoleFilter === 'ALL'
                  ? 'Yeni bir kullanc oluturarak balayn.'
                  : 'Farkl bir filtre sein veya yeni kullanc oluturun.'}
              </p>
            </div>
          )}
        </CardContent>
      </Card>
      )}

      {/* Roles Table */}
      {activeTab === 'roles' && (
      <Card>
        <CardHeader>
          <CardTitle>Roller</CardTitle>
          <CardDescription>
            Sistemdeki tm roller ve aklamalar
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Rol Ad
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Aklama
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Kullanc Says
                  </th>
                  <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                    lemler
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {roles.map((role) => (
                  <tr key={role.id} className="hover:bg-gray-50 transition-colors">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <Shield className={`h-5 w-5 mr-3 ${
                          role.name === 'ADMIN' ? 'text-red-600' :
                          role.name === 'TEACHER' ? 'text-purple-600' :
                          role.name === 'STUDENT' ? 'text-yellow-600' :
                          'text-gray-600'
                        }`} />
                        <span className={`px-3 py-1 text-sm font-semibold rounded-full border ${getRoleBadgeColor(role.name)}`}>
                          {role.name}
                        </span>
                      </div>
                    </td>
                    <td className="px-6 py-4">
                      <div className="text-sm text-gray-900">
                        {role.description || 'Aklama yok'}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-900">
                        {users.filter(u => u.roles?.includes(role.name)).length} kullanc
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                      <div className="flex justify-end gap-2">
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => openEditRoleModal(role)}
                          className="text-indigo-600 hover:text-indigo-900"
                        >
                          <Edit className="h-4 w-4" />
                        </Button>
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => handleDeleteRole(role)}
                          className="text-red-600 hover:text-red-900"
                          disabled={users.filter(u => u.roles?.includes(role.name)).length > 0}
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {roles.length === 0 && (
            <div className="text-center py-12">
              <Shield className="mx-auto h-12 w-12 text-gray-400" />
              <h3 className="mt-2 text-sm font-medium text-gray-900">
                Rol bulunamad
              </h3>
              <p className="mt-1 text-sm text-gray-500">
                Yeni bir rol oluturarak balayn.
              </p>
            </div>
          )}
        </CardContent>
      </Card>
      )}

      {/* Create/Edit User Modal */}
      <AnimatePresence>
        {isModalOpen && activeTab === 'users' && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50"
            onClick={closeModal}
          >
            <motion.div
              initial={{ scale: 0.95, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.95, opacity: 0 }}
              className="bg-white rounded-xl shadow-2xl max-w-md w-full p-6"
              onClick={(e) => e.stopPropagation()}
            >
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-2xl font-bold text-gray-900">
                  {editingUser ? 'Kullanc Dzenle' : 'Yeni Kullanc'}
                </h2>
                <button
                  onClick={closeModal}
                  className="text-gray-400 hover:text-gray-600 transition-colors"
                >
                  <X className="h-6 w-6" />
                </button>
              </div>

              <form onSubmit={handleSubmit} className="space-y-4">
                {/* Email */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Email *
                  </label>
                  <div className="relative">
                    <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
                    <Input
                      type="email"
                      value={formData.email}
                      onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                      className="pl-10"
                      placeholder="kullanici@bilgen.com"
                      required
                    />
                  </div>
                </div>

                {/* Name */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    sim
                  </label>
                  <Input
                    type="text"
                    value={formData.name}
                    onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                    placeholder="Kullanc Ad"
                  />
                </div>

                {/* Password */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    ifre {!editingUser && '*'}
                  </label>
                  <div className="relative">
                    <Lock className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
                    <Input
                      type="password"
                      value={formData.password}
                      onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                      className="pl-10"
                      placeholder={editingUser ? 'Bo brakn (deitirmek iin)' : 'En az 6 karakter'}
                      required={!editingUser}
                      minLength={6}
                    />
                  </div>
                  {editingUser && (
                    <p className="text-xs text-gray-500 mt-1">
                      ifreyi deitirmek istemiyorsanz bo brakn
                    </p>
                  )}
                </div>

                {/* Roles */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Roller
                  </label>
                  <div className="space-y-2">
                    {roles.map((role) => (
                      <label
                        key={role.id}
                        className="flex items-center p-3 border rounded-lg hover:bg-gray-50 cursor-pointer transition-colors"
                      >
                        <input
                          type="checkbox"
                          checked={formData.roles.includes(role.name)}
                          onChange={() => toggleRole(role.name)}
                          className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
                        />
                        <div className="ml-3 flex-1">
                          <span className="text-sm font-medium text-gray-900">
                            {role.name}
                          </span>
                          {role.description && (
                            <p className="text-xs text-gray-500">{role.description}</p>
                          )}
                        </div>
                        <span className={`px-2 py-1 text-xs font-semibold rounded-full ${getRoleBadgeColor(role.name)}`}>
                          {role.name}
                        </span>
                      </label>
                    ))}
                  </div>
                </div>

                {/* Warning for self-edit */}
                {editingUser && editingUser.roles?.includes('ADMIN') && !formData.roles.includes('ADMIN') && (
                  <div className="flex items-start gap-2 p-3 bg-yellow-50 border border-yellow-200 rounded-lg">
                    <AlertCircle className="h-5 w-5 text-yellow-600 flex-shrink-0 mt-0.5" />
                    <div className="text-sm text-yellow-800">
                      <p className="font-medium">Dikkat!</p>
                      <p>Kendi admin yetkilerinizi kaldramazsnz.</p>
                    </div>
                  </div>
                )}

                {/* Actions */}
                <div className="flex gap-3 pt-4">
                  <Button
                    type="button"
                    variant="outline"
                    onClick={closeModal}
                    className="flex-1"
                  >
                    ptal
                  </Button>
                  <Button
                    type="submit"
                    className="flex-1 flex items-center justify-center gap-2"
                    disabled={createMutation.isPending || updateMutation.isPending}
                  >
                    {(createMutation.isPending || updateMutation.isPending) ? (
                      <>
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                        Kaydediliyor...
                      </>
                    ) : (
                      <>
                        <Save className="h-4 w-4" />
                        {editingUser ? 'Gncelle' : 'Olutur'}
                      </>
                    )}
                  </Button>
                </div>
              </form>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Create/Edit Role Modal */}
      <AnimatePresence>
        {isModalOpen && activeTab === 'roles' && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50"
            onClick={closeModal}
          >
            <motion.div
              initial={{ scale: 0.95, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.95, opacity: 0 }}
              className="bg-white rounded-xl shadow-2xl max-w-md w-full p-6"
              onClick={(e) => e.stopPropagation()}
            >
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
                  <Shield className="h-6 w-6 text-indigo-600" />
                  {editingRole ? 'Rol Dzenle' : 'Yeni Rol'}
                </h2>
                <button
                  onClick={closeModal}
                  className="text-gray-400 hover:text-gray-600 transition-colors"
                >
                  <X className="h-6 w-6" />
                </button>
              </div>

              <form onSubmit={handleSubmitRole} className="space-y-4">
                {/* Role Name */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Rol Ad *
                  </label>
                  <div className="relative">
                    <Shield className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
                    <Input
                      type="text"
                      value={roleFormData.name}
                      onChange={(e) => setRoleFormData({ ...roleFormData, name: e.target.value })}
                      className="pl-10 uppercase"
                      placeholder="ORNEK_ROL"
                      required
                    />
                  </div>
                  <p className="text-xs text-gray-500 mt-1">
                    Rol ad otomatik olarak byk harfe evrilecek
                  </p>
                </div>

                {/* Description */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Aklama
                  </label>
                  <div className="relative">
                    <FileText className="absolute left-3 top-3 h-5 w-5 text-gray-400" />
                    <textarea
                      value={roleFormData.description}
                      onChange={(e) => setRoleFormData({ ...roleFormData, description: e.target.value })}
                      className="w-full pl-10 pr-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 resize-none"
                      placeholder="Roln ne iin kullanlacan aklayn"
                      rows="3"
                    />
                  </div>
                </div>

                {/* Info Note */}
                <div className="flex items-start gap-2 p-3 bg-blue-50 border border-blue-200 rounded-lg">
                  <AlertCircle className="h-5 w-5 text-blue-600 flex-shrink-0 mt-0.5" />
                  <div className="text-sm text-blue-800">
                    <p className="font-medium">Bilgi</p>
                    <p>Oluturulan rol, kullanclara atanabilir hale gelecektir.</p>
                  </div>
                </div>

                {/* Actions */}
                <div className="flex gap-3 pt-4">
                  <Button
                    type="button"
                    variant="outline"
                    onClick={closeModal}
                    className="flex-1"
                  >
                    ptal
                  </Button>
                  <Button
                    type="submit"
                    className="flex-1 flex items-center justify-center gap-2"
                    disabled={createRoleMutation.isPending || updateRoleMutation.isPending}
                  >
                    {(createRoleMutation.isPending || updateRoleMutation.isPending) ? (
                      <>
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                        Kaydediliyor...
                      </>
                    ) : (
                      <>
                        <Save className="h-4 w-4" />
                        {editingRole ? 'Gncelle' : 'Olutur'}
                      </>
                    )}
                  </Button>
                </div>
              </form>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default AdminPage;

