import React, { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { motion } from 'framer-motion';
import { Toaster } from 'sonner';

import useAuthStore from './store/authStore';
import ProtectedRoute from './components/ProtectedRoute';
import Navigation from './components/Navigation';
import Sidebar from './components/Sidebar';
import { cn } from './lib/utils';
import { getDefaultRoute } from './lib/routeHelpers';

import GeneratorPage from './pages/GeneratorPage';
import ReviewPage from './pages/ReviewPage';
import GalleryPage from './pages/GalleryPage';
import TemplatePage from './pages/TemplatePage';
import AdminPage from './pages/AdminPage';
import LoginPage from './pages/LoginPage';
import TeacherPage from './pages/TeacherPage';
import StudentPage from './pages/StudentPage';
import ClassroomPage from './pages/ClassroomPage';

// Create Query Client
const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      retry: 1,
      staleTime: 5 * 60 * 1000, // 5 minutes
    },
  },
});

// Page transitions animation
const pageVariants = {
  initial: {
    opacity: 0,
    y: 20,
  },
  in: {
    opacity: 1,
    y: 0,
  },
  out: {
    opacity: 0,
    y: -20,
  },
};

const pageTransition = {
  type: "tween",
  ease: "anticipate",
  duration: 0.5,
};

function HomeRoute() {
  const { user, hasRole } = useAuthStore();
  const isGeneratorHome = hasRole('TEACHER') || hasRole('ADMIN');

  if (isGeneratorHome) {
    return (
      <motion.div
        initial="initial"
        animate="in"
        exit="out"
        variants={pageVariants}
        transition={pageTransition}
      >
        <GeneratorPage />
      </motion.div>
    );
  }

  if (hasRole('TEACHER')) {
    return (
      <motion.div
        initial="initial"
        animate="in"
        exit="out"
        variants={pageVariants}
        transition={pageTransition}
      >
        <ClassroomPage />
      </motion.div>
    );
  }

  if (hasRole('STUDENT')) {
    return (
      <motion.div
        initial="initial"
        animate="in"
        exit="out"
        variants={pageVariants}
        transition={pageTransition}
      >
        <StudentPage />
      </motion.div>
    );
  }

  const target = getDefaultRoute(user);
  return <Navigate to={target} replace />;
}

function App() {
  const initialize = useAuthStore((state) => state.initialize);
  const isAuthenticated = useAuthStore((state) => state.isAuthenticated);

  useEffect(() => {
    initialize();
  }, [initialize]);

  return (
    <QueryClientProvider client={queryClient}>
      <Router>
        <div className="min-h-screen bg-gradient-to-br from-slate-50 via-blue-50 to-indigo-100">
          {/* Sidebar Navigation */}
          <Sidebar />

          {/* Main Content with dynamic margin for sidebar */}
          <main className={cn(
            "min-h-screen transition-all duration-300",
            isAuthenticated ? "lg:ml-[280px]" : ""
          )}>
            <div className="max-w-7xl mx-auto py-8 px-4 sm:px-6 lg:px-8">
              <Routes>
                {/* Public Route */}
                <Route
                  path="/login"
                  element={
                    <motion.div
                      initial="initial"
                      animate="in"
                      exit="out"
                      variants={pageVariants}
                      transition={pageTransition}
                    >
                      <LoginPage />
                    </motion.div>
                  }
                />

                {/* Protected Routes */}
                <Route
                  path="/"
                  element={
                    <ProtectedRoute>
                      <HomeRoute />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/review"
                  element={
                    <ProtectedRoute requiredRoles={["TEACHER", "ADMIN"]}>
                      <motion.div
                        initial="initial"
                        animate="in"
                        exit="out"
                        variants={pageVariants}
                        transition={pageTransition}
                      >
                        <ReviewPage />
                      </motion.div>
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/gallery"
                  element={
                    <motion.div
                      initial="initial"
                      animate="in"
                      exit="out"
                      variants={pageVariants}
                      transition={pageTransition}
                    >
                      <GalleryPage />
                    </motion.div>
                  }
                />
                <Route
                  path="/templates"
                  element={
                    <ProtectedRoute requiredRoles={["ADMIN"]}>
                      <motion.div
                        initial="initial"
                        animate="in"
                        exit="out"
                        variants={pageVariants}
                        transition={pageTransition}
                      >
                        <TemplatePage />
                      </motion.div>
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/admin"
                  element={
                    <ProtectedRoute requiredRoles={["ADMIN"]}>
                      <motion.div
                        initial="initial"
                        animate="in"
                        exit="out"
                        variants={pageVariants}
                        transition={pageTransition}
                      >
                        <AdminPage />
                      </motion.div>
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/teacher"
                  element={
                    <ProtectedRoute requiredRoles={["TEACHER", "ADMIN"]}>
                      <motion.div
                        initial="initial"
                        animate="in"
                        exit="out"
                        variants={pageVariants}
                        transition={pageTransition}
                      >
                        <TeacherPage />
                      </motion.div>
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/classrooms"
                  element={
                    <ProtectedRoute requiredRoles={["TEACHER", "ADMIN"]}>
                      <motion.div
                        initial="initial"
                        animate="in"
                        exit="out"
                        variants={pageVariants}
                        transition={pageTransition}
                      >
                        <ClassroomPage />
                      </motion.div>
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/student"
                  element={
                    <ProtectedRoute requiredRoles={["STUDENT"]}>
                      <motion.div
                        initial="initial"
                        animate="in"
                        exit="out"
                        variants={pageVariants}
                        transition={pageTransition}
                      >
                        <StudentPage />
                      </motion.div>
                    </ProtectedRoute>
                  }
                />

                {/* Fallback */}
                <Route
                  path="*"
                  element={
                    <Navigate
                      to={useAuthStore.getState().isAuthenticated ? "/" : "/login"}
                      replace
                    />
                  }
                />
              </Routes>
            </div>
          </main>

          {/* Modern Toast Notifications */}
          <Toaster
            position="top-right"
            closeButton
            richColors
            theme="light"
          />
        </div>
      </Router>
    </QueryClientProvider>
  );
}

export default App;
