import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { toast } from 'sonner';
import {
  Users,
  Plus,
  Edit,
  Trash2,
  X,
  UserPlus,
  UserMinus,
  Megaphone,
  School,
  GraduationCap,
  ChevronRight,
  BookOpen
} from 'lucide-react';

import { classroomsAPI } from '../services/api';
import { Button } from '../components/ui/Button';
import { Card, CardHeader, CardTitle, CardContent } from '../components/ui/Card';
import useAuthStore from '../store/authStore';

// Grade options
const GRADES = [1, 2, 3, 4, 5, 6, 7, 8];
const SECTION_OPTIONS = ['A', 'B', 'C', 'D'];

const getSectionFromName = (name) => {
  if (!name) return '';
  const parts = name.split('-');
  if (parts.length > 1) {
    const candidate = parts[1].trim().toUpperCase();
    if (SECTION_OPTIONS.includes(candidate)) {
      return candidate;
    }
  }
  const trimmed = name.trim();
  if (!trimmed) return '';
  const lastChar = trimmed.slice(-1).toUpperCase();
  return SECTION_OPTIONS.includes(lastChar) ? lastChar : '';
};

// Modal Component
function Modal({ isOpen, onClose, title, children }) {
  if (!isOpen) return null;

  return (
    <AnimatePresence>
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4"
        onClick={onClose}
      >
        <motion.div
          initial={{ scale: 0.9, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          exit={{ scale: 0.9, opacity: 0 }}
          className="bg-white rounded-xl shadow-2xl max-w-lg w-full max-h-[90vh] overflow-y-auto"
          onClick={(e) => e.stopPropagation()}
        >
          <div className="flex items-center justify-between p-4 border-b">
            <h2 className="text-lg font-semibold text-gray-800">{title}</h2>
            <button
              onClick={onClose}
              className="p-1 hover:bg-gray-100 rounded-full transition-colors"
            >
              <X className="h-5 w-5 text-gray-500" />
            </button>
          </div>
          <div className="p-4">{children}</div>
        </motion.div>
      </motion.div>
    </AnimatePresence>
  );
}

// Classroom Card Component
function ClassroomCard({ classroom, onSelect, onEdit, onDelete, isSelected }) {
  return (
    <motion.div
      whileHover={{ scale: 1.02 }}
      whileTap={{ scale: 0.98 }}
      className={`cursor-pointer transition-all ${
        isSelected ? 'ring-2 ring-indigo-500' : ''
      }`}
      onClick={() => onSelect(classroom)}
    >
      <Card className="bg-white hover:shadow-lg transition-shadow">
        <CardContent className="p-4">
          <div className="flex items-start justify-between">
            <div className="flex items-center space-x-3">
              <div className="p-2 bg-indigo-100 rounded-lg">
                <School className="h-6 w-6 text-indigo-600" />
              </div>
              <div>
                <h3 className="font-semibold text-gray-800">{classroom.name}</h3>
                <p className="text-sm text-gray-500">{classroom.grade}. Snf</p>
              </div>
            </div>
            <div className="flex items-center space-x-1">
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  onEdit(classroom);
                }}
                className="p-1.5 hover:bg-gray-100 rounded-lg transition-colors"
              >
                <Edit className="h-4 w-4 text-gray-500" />
              </button>
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  onDelete(classroom);
                }}
                className="p-1.5 hover:bg-red-50 rounded-lg transition-colors"
              >
                <Trash2 className="h-4 w-4 text-red-500" />
              </button>
            </div>
          </div>

          <div className="mt-3 flex items-center justify-between text-sm">
            <div className="flex items-center text-gray-500">
              <Users className="h-4 w-4 mr-1" />
              <span>{classroom._count?.students || 0} renci</span>
            </div>
            <div className="flex items-center text-gray-500">
              <Megaphone className="h-4 w-4 mr-1" />
              <span>{classroom._count?.announcements || 0} duyuru</span>
            </div>
          </div>

          {classroom.description && (
            <p className="mt-2 text-sm text-gray-500 line-clamp-2">
              {classroom.description}
            </p>
          )}
        </CardContent>
      </Card>
    </motion.div>
  );
}

// Create/Edit Classroom Form
function ClassroomForm({ classroom, onSubmit, onCancel, teacherOptions = [], currentUserId, isAdmin }) {
  const [formData, setFormData] = useState({
    grade: classroom?.grade ? String(classroom.grade) : '',
    section: getSectionFromName(classroom?.name),
    description: classroom?.description || '',
    teacherId: (classroom?.teacher?.id ?? currentUserId)?.toString() || ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    setFormData({
      grade: classroom?.grade ? String(classroom.grade) : '',
      section: getSectionFromName(classroom?.name),
      description: classroom?.description || '',
      teacherId: (classroom?.teacher?.id ?? currentUserId)?.toString() || ''
    });
  }, [classroom, currentUserId]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!formData.grade || !formData.section) {
      toast.error('Snf seviyesi ve ubesi gereklidir');
      return;
    }
    if (isAdmin && !formData.teacherId) {
      toast.error('Ltfen retmen sein');
      return;
    }

    const payload = {
      grade: formData.grade,
      section: formData.section,
      description: formData.description
    };

    if (formData.teacherId) {
      payload.teacherId = formData.teacherId;
    }

    setIsSubmitting(true);
    await onSubmit(payload);
    setIsSubmitting(false);
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          Snf ubesi *
        </label>
        <select
          value={formData.section}
          onChange={(e) => setFormData({ ...formData, section: e.target.value })}
          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
          required
        >
          <option value="">ube seiniz</option>
          {SECTION_OPTIONS.map((sectionOption) => (
            <option key={sectionOption} value={sectionOption}>
              {sectionOption} ubesi
            </option>
          ))}
        </select>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          Snf Seviyesi *
        </label>
        <select
          value={formData.grade}
          onChange={(e) => setFormData({ ...formData, grade: e.target.value })}
          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
          required
        >
          <option value="">Seiniz</option>
          {GRADES.map((grade) => (
            <option key={grade} value={grade}>
              {grade}. Snf
            </option>
          ))}
        </select>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          retmen *
        </label>
        <select
          value={formData.teacherId}
          onChange={(e) => setFormData({ ...formData, teacherId: e.target.value })}
          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
          disabled={!isAdmin}
          required={isAdmin}
        >
          <option value="">{teacherOptions.length ? 'retmen seiniz' : 'retmen bulunamad'}</option>
          {teacherOptions.map((teacher) => (
            <option key={teacher.id} value={teacher.id}>
              {teacher.name || teacher.email}
            </option>
          ))}
        </select>
        {!isAdmin && (
          <p className="text-xs text-gray-500 mt-1">
            retmen atamasn yalnzca yneticiler deitirebilir.
          </p>
        )}
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          Aklama
        </label>
        <textarea
          value={formData.description}
          onChange={(e) => setFormData({ ...formData, description: e.target.value })}
          placeholder="Snf hakknda ksa aklama..."
          rows={3}
          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
        />
      </div>

      <div className="flex justify-end space-x-2 pt-2">
        <Button type="button" variant="outline" onClick={onCancel}>
          ptal
        </Button>
        <Button type="submit" disabled={isSubmitting}>
          {isSubmitting ? 'Kaydediliyor...' : classroom ? 'Gncelle' : 'Olutur'}
        </Button>
      </div>
    </form>
  );
}

// Student List Component
function StudentList({ students, onRemove }) {
  if (!students || students.length === 0) {
    return (
      <div className="text-center py-8 text-gray-500">
        <GraduationCap className="h-12 w-12 mx-auto mb-2 opacity-50" />
        <p>Bu snfta henz renci yok</p>
      </div>
    );
  }

  return (
    <div className="space-y-2">
      {students.map((student) => (
        <div
          key={student.id}
          className="flex items-center justify-between p-3 bg-gray-50 rounded-lg"
        >
          <div className="flex items-center space-x-3">
            <div className="w-8 h-8 bg-indigo-100 rounded-full flex items-center justify-center">
              <span className="text-indigo-600 font-medium text-sm">
                {(student.name || student.email).charAt(0).toUpperCase()}
              </span>
            </div>
            <div>
              <p className="font-medium text-gray-800">{student.name || 'simsiz'}</p>
              <p className="text-sm text-gray-500">{student.email}</p>
            </div>
          </div>
          <button
            onClick={() => onRemove(student)}
            className="p-1.5 hover:bg-red-100 rounded-lg transition-colors"
            title="Snftan kar"
          >
            <UserMinus className="h-4 w-4 text-red-500" />
          </button>
        </div>
      ))}
    </div>
  );
}

// Add Student Modal Content
function AddStudentForm({ classroomId, onSuccess, onCancel }) {
  const [students, setStudents] = useState([]);
  const [selectedStudent, setSelectedStudent] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [isAdding, setIsAdding] = useState(false);

  useEffect(() => {
    loadUnassignedStudents();
  }, []);

  const loadUnassignedStudents = async () => {
    try {
      const response = await classroomsAPI.getUnassignedStudents();
      setStudents(response.data || []);
    } catch (error) {
      toast.error('renciler yklenirken hata olutu');
    } finally {
      setIsLoading(false);
    }
  };

  const handleAdd = async () => {
    if (!selectedStudent) {
      toast.error('Ltfen bir renci sein');
      return;
    }
    setIsAdding(true);
    try {
      await classroomsAPI.addStudent(classroomId, parseInt(selectedStudent));
      toast.success('renci snfa eklendi');
      onSuccess();
    } catch (error) {
      toast.error(error.message || 'renci eklenirken hata olutu');
    } finally {
      setIsAdding(false);
    }
  };

  if (isLoading) {
    return <div className="text-center py-4">Ykleniyor...</div>;
  }

  if (students.length === 0) {
    return (
      <div className="text-center py-8 text-gray-500">
        <Users className="h-12 w-12 mx-auto mb-2 opacity-50" />
        <p>Atanabilecek renci bulunmuyor</p>
        <p className="text-sm mt-1">Tm renciler bir snfa atanm</p>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          renci Sein
        </label>
        <select
          value={selectedStudent}
          onChange={(e) => setSelectedStudent(e.target.value)}
          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
        >
          <option value="">Seiniz</option>
          {students.map((student) => (
            <option key={student.id} value={student.id}>
              {student.name || student.email}
            </option>
          ))}
        </select>
      </div>

      <div className="flex justify-end space-x-2">
        <Button type="button" variant="outline" onClick={onCancel}>
          ptal
        </Button>
        <Button onClick={handleAdd} disabled={isAdding}>
          {isAdding ? 'Ekleniyor...' : 'Ekle'}
        </Button>
      </div>
    </div>
  );
}

// Announcement Form
function AnnouncementForm({ classroomId, onSuccess, onCancel }) {
  const [formData, setFormData] = useState({ title: '', content: '' });
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!formData.title || !formData.content) {
      toast.error('Balk ve ierik gereklidir');
      return;
    }
    setIsSubmitting(true);
    try {
      await classroomsAPI.createAnnouncement(classroomId, formData);
      toast.success('Duyuru oluturuldu');
      onSuccess();
    } catch (error) {
      toast.error(error.message || 'Duyuru oluturulurken hata olutu');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          Balk *
        </label>
        <input
          type="text"
          value={formData.title}
          onChange={(e) => setFormData({ ...formData, title: e.target.value })}
          placeholder="Duyuru bal"
          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
          required
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          erik *
        </label>
        <textarea
          value={formData.content}
          onChange={(e) => setFormData({ ...formData, content: e.target.value })}
          placeholder="Duyuru ierii..."
          rows={4}
          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
          required
        />
      </div>

      <div className="flex justify-end space-x-2">
        <Button type="button" variant="outline" onClick={onCancel}>
          ptal
        </Button>
        <Button type="submit" disabled={isSubmitting}>
          {isSubmitting ? 'Gnderiliyor...' : 'Yaynla'}
        </Button>
      </div>
    </form>
  );
}

// Announcement List
function AnnouncementList({ announcements, onDelete }) {
  if (!announcements || announcements.length === 0) {
    return (
      <div className="text-center py-8 text-gray-500">
        <Megaphone className="h-12 w-12 mx-auto mb-2 opacity-50" />
        <p>Henz duyuru yok</p>
      </div>
    );
  }

  return (
    <div className="space-y-3">
      {announcements.map((announcement) => (
        <div
          key={announcement.id}
          className="p-4 bg-gray-50 rounded-lg border-l-4 border-indigo-500"
        >
          <div className="flex items-start justify-between">
            <div>
              <h4 className="font-medium text-gray-800">{announcement.title}</h4>
              <p className="text-sm text-gray-600 mt-1">{announcement.content}</p>
              <p className="text-xs text-gray-400 mt-2">
                {new Date(announcement.createdAt).toLocaleDateString('tr-TR', {
                  day: 'numeric',
                  month: 'long',
                  year: 'numeric',
                  hour: '2-digit',
                  minute: '2-digit'
                })}
                {announcement.createdBy && `  ${announcement.createdBy.name || announcement.createdBy.email}`}
              </p>
            </div>
            <button
              onClick={() => onDelete(announcement)}
              className="p-1.5 hover:bg-red-100 rounded-lg transition-colors"
            >
              <Trash2 className="h-4 w-4 text-red-500" />
            </button>
          </div>
        </div>
      ))}
    </div>
  );
}

// Main Classroom Page Component
export default function ClassroomPage() {
  const { user, hasRole } = useAuthStore();
  const [classrooms, setClassrooms] = useState([]);
  const [selectedClassroom, setSelectedClassroom] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [teachers, setTeachers] = useState([]);
  const isAdmin = hasRole('ADMIN');

  // Modal states
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [showAddStudentModal, setShowAddStudentModal] = useState(false);
  const [showAnnouncementModal, setShowAnnouncementModal] = useState(false);
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [classroomToEdit, setClassroomToEdit] = useState(null);
  const [classroomToDelete, setClassroomToDelete] = useState(null);

  useEffect(() => {
    loadClassrooms();
  }, []);

  useEffect(() => {
    loadTeachers();
  }, []);

  const loadClassrooms = async () => {
    try {
      const response = await classroomsAPI.getAll();
      setClassrooms(response.data || []);
    } catch (error) {
      toast.error('Snflar yklenirken hata olutu');
    } finally {
      setIsLoading(false);
    }
  };

  const loadClassroomDetails = async (classroomId) => {
    try {
      const response = await classroomsAPI.getById(classroomId);
      setSelectedClassroom(response.data);
    } catch (error) {
      toast.error('Snf detaylar yklenirken hata olutu');
    }
  };

  const loadTeachers = async () => {
    try {
      const response = await classroomsAPI.getTeachers();
      setTeachers(response.data || []);
    } catch (error) {
      toast.error('retmenler yklenirken hata olutu');
    }
  };

  const handleSelectClassroom = (classroom) => {
    loadClassroomDetails(classroom.id);
  };

  const handleCreateClassroom = async (data) => {
    try {
      await classroomsAPI.create(data);
      toast.success('Snf oluturuldu');
      setShowCreateModal(false);
      loadClassrooms();
    } catch (error) {
      toast.error(error.message || 'Snf oluturulurken hata olutu');
    }
  };

  const handleEditClassroom = async (data) => {
    try {
      await classroomsAPI.update(classroomToEdit.id, data);
      toast.success('Snf gncellendi');
      setShowEditModal(false);
      setClassroomToEdit(null);
      loadClassrooms();
      if (selectedClassroom?.id === classroomToEdit.id) {
        loadClassroomDetails(classroomToEdit.id);
      }
    } catch (error) {
      toast.error(error.message || 'Snf gncellenirken hata olutu');
    }
  };

  const handleDeleteClassroom = async () => {
    try {
      await classroomsAPI.delete(classroomToDelete.id);
      toast.success('Snf silindi');
      setShowDeleteConfirm(false);
      setClassroomToDelete(null);
      if (selectedClassroom?.id === classroomToDelete.id) {
        setSelectedClassroom(null);
      }
      loadClassrooms();
    } catch (error) {
      toast.error(error.message || 'Snf silinirken hata olutu');
    }
  };

  const handleRemoveStudent = async (student) => {
    if (!window.confirm(`${student.name || student.email} rencisini snftan karmak istediinize emin misiniz?`)) {
      return;
    }
    try {
      await classroomsAPI.removeStudent(selectedClassroom.id, student.id);
      toast.success('renci snftan karld');
      loadClassroomDetails(selectedClassroom.id);
      loadClassrooms();
    } catch (error) {
      toast.error(error.message || 'renci karlrken hata olutu');
    }
  };

  const handleDeleteAnnouncement = async (announcement) => {
    if (!window.confirm('Bu duyuruyu silmek istediinize emin misiniz?')) {
      return;
    }
    try {
      await classroomsAPI.deleteAnnouncement(selectedClassroom.id, announcement.id);
      toast.success('Duyuru silindi');
      loadClassroomDetails(selectedClassroom.id);
    } catch (error) {
      toast.error(error.message || 'Duyuru silinirken hata olutu');
    }
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-indigo-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-800">Snf Ynetimi</h1>
          <p className="text-gray-500">Snflarnz ynetin, renci ekleyin ve duyuru paylan</p>
        </div>
        <Button onClick={() => setShowCreateModal(true)}>
          <Plus className="h-4 w-4 mr-2" />
          Yeni Snf
        </Button>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Left Panel - Classroom List */}
        <div className="lg:col-span-1 space-y-4">
          <h2 className="font-semibold text-gray-700 flex items-center">
            <BookOpen className="h-5 w-5 mr-2" />
            Snflarm ({classrooms.length})
          </h2>

          {classrooms.length === 0 ? (
            <Card className="bg-white">
              <CardContent className="py-12 text-center text-gray-500">
                <School className="h-12 w-12 mx-auto mb-3 opacity-50" />
                <p>Henz snf oluturmadnz</p>
                <Button
                  variant="outline"
                  className="mt-4"
                  onClick={() => setShowCreateModal(true)}
                >
                  <Plus className="h-4 w-4 mr-2" />
                  lk Snfnz Oluturun
                </Button>
              </CardContent>
            </Card>
          ) : (
            <div className="space-y-3">
              {classrooms.map((classroom) => (
                <ClassroomCard
                  key={classroom.id}
                  classroom={classroom}
                  isSelected={selectedClassroom?.id === classroom.id}
                  onSelect={handleSelectClassroom}
                  onEdit={(c) => {
                    setClassroomToEdit(c);
                    setShowEditModal(true);
                  }}
                  onDelete={(c) => {
                    setClassroomToDelete(c);
                    setShowDeleteConfirm(true);
                  }}
                />
              ))}
            </div>
          )}
        </div>

        {/* Right Panel - Classroom Details */}
        <div className="lg:col-span-2">
          {selectedClassroom ? (
            <Card className="bg-white">
              <CardHeader className="border-b">
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle className="flex items-center">
                      <School className="h-6 w-6 mr-2 text-indigo-600" />
                      {selectedClassroom.name}
                    </CardTitle>
                    <p className="text-sm text-gray-500 mt-1">
                      {selectedClassroom.grade}. Snf
                      {selectedClassroom.description && `  ${selectedClassroom.description}`}
                    </p>
                  </div>
                </div>
              </CardHeader>
              <CardContent className="pt-6">
                {/* Students Section */}
                <div className="mb-8">
                  <div className="flex items-center justify-between mb-4">
                    <h3 className="font-semibold text-gray-700 flex items-center">
                      <Users className="h-5 w-5 mr-2" />
                      renciler ({selectedClassroom.students?.length || 0})
                    </h3>
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => setShowAddStudentModal(true)}
                    >
                      <UserPlus className="h-4 w-4 mr-1" />
                      renci Ekle
                    </Button>
                  </div>
                  <StudentList
                    students={selectedClassroom.students}
                    onRemove={handleRemoveStudent}
                  />
                </div>

                {/* Announcements Section */}
                <div>
                  <div className="flex items-center justify-between mb-4">
                    <h3 className="font-semibold text-gray-700 flex items-center">
                      <Megaphone className="h-5 w-5 mr-2" />
                      Duyurular ({selectedClassroom.announcements?.length || 0})
                    </h3>
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => setShowAnnouncementModal(true)}
                    >
                      <Plus className="h-4 w-4 mr-1" />
                      Yeni Duyuru
                    </Button>
                  </div>
                  <AnnouncementList
                    announcements={selectedClassroom.announcements}
                    onDelete={handleDeleteAnnouncement}
                  />
                </div>
              </CardContent>
            </Card>
          ) : (
            <Card className="bg-white">
              <CardContent className="py-16 text-center text-gray-500">
                <ChevronRight className="h-12 w-12 mx-auto mb-3 opacity-50" />
                <p>Detaylar grmek iin bir snf sein</p>
              </CardContent>
            </Card>
          )}
        </div>
      </div>

      {/* Create Classroom Modal */}
      <Modal
        isOpen={showCreateModal}
        onClose={() => setShowCreateModal(false)}
        title="Yeni Snf Olutur"
      >
        <ClassroomForm
          onSubmit={handleCreateClassroom}
          onCancel={() => setShowCreateModal(false)}
          teacherOptions={teachers}
          currentUserId={user?.id}
          isAdmin={isAdmin}
        />
      </Modal>

      {/* Edit Classroom Modal */}
      <Modal
        isOpen={showEditModal}
        onClose={() => {
          setShowEditModal(false);
          setClassroomToEdit(null);
        }}
        title="Snf Dzenle"
      >
        <ClassroomForm
          classroom={classroomToEdit}
          onSubmit={handleEditClassroom}
          onCancel={() => {
            setShowEditModal(false);
            setClassroomToEdit(null);
          }}
          teacherOptions={teachers}
          currentUserId={user?.id}
          isAdmin={isAdmin}
        />
      </Modal>

      {/* Add Student Modal */}
      <Modal
        isOpen={showAddStudentModal}
        onClose={() => setShowAddStudentModal(false)}
        title="renci Ekle"
      >
        <AddStudentForm
          classroomId={selectedClassroom?.id}
          onSuccess={() => {
            setShowAddStudentModal(false);
            loadClassroomDetails(selectedClassroom.id);
            loadClassrooms();
          }}
          onCancel={() => setShowAddStudentModal(false)}
        />
      </Modal>

      {/* Announcement Modal */}
      <Modal
        isOpen={showAnnouncementModal}
        onClose={() => setShowAnnouncementModal(false)}
        title="Yeni Duyuru"
      >
        <AnnouncementForm
          classroomId={selectedClassroom?.id}
          onSuccess={() => {
            setShowAnnouncementModal(false);
            loadClassroomDetails(selectedClassroom.id);
          }}
          onCancel={() => setShowAnnouncementModal(false)}
        />
      </Modal>

      {/* Delete Confirmation Modal */}
      <Modal
        isOpen={showDeleteConfirm}
        onClose={() => {
          setShowDeleteConfirm(false);
          setClassroomToDelete(null);
        }}
        title="Snf Sil"
      >
        <div className="space-y-4">
          <p className="text-gray-600">
            <strong>{classroomToDelete?.name}</strong> snfn silmek istediinize emin misiniz?
          </p>
          <p className="text-sm text-red-600 bg-red-50 p-3 rounded-lg">
             Bu ilem geri alnamaz. Snftaki rencilerin bu retmenle ilikili devleri de silinecektir.
          </p>
          <div className="flex justify-end space-x-2">
            <Button
              variant="outline"
              onClick={() => {
                setShowDeleteConfirm(false);
                setClassroomToDelete(null);
              }}
            >
              ptal
            </Button>
            <Button
              variant="destructive"
              onClick={handleDeleteClassroom}
              className="bg-red-600 hover:bg-red-700"
            >
              <Trash2 className="h-4 w-4 mr-1" />
              Sil
            </Button>
          </div>
        </div>
      </Modal>
    </div>
  );
}

