const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';

// Helper function for fetch requests with token support
async function fetchAPI(endpoint, options = {}) {
  const url = `${API_BASE_URL}${endpoint}`;
  
  // Get token from localStorage
  const token = localStorage.getItem('token');

  const config = {
    headers: {
      'Content-Type': 'application/json',
      ...(token && { Authorization: `Bearer ${token}` }),
      ...options.headers,
    },
    ...options,
  };
  
  try {
    const response = await fetch(url, config);
    const data = await response.json();
    
    if (!response.ok) {
      // If unauthorized, clear token and reload
      if (response.status === 401) {
        localStorage.removeItem('token');
        window.location.href = '/login';
      }
      throw new Error(data.message || data.error || 'API request failed');
    }
    
    return data;
  } catch (error) {
    console.error('API Error:', error);
    throw error;
  }
}

// Auth API
export const authAPI = {
  login: async (email, password) => {
    return fetchAPI('/auth/login', {
      method: 'POST',
      body: JSON.stringify({ email, password }),
    });
  },

  getProfile: async () => {
    return fetchAPI('/auth/profile');
  }
};

// Images API
export const imagesAPI = {
  // Generate new image
  generate: async (data) => {
    return fetchAPI('/images/generate', {
      method: 'POST',
      body: JSON.stringify(data),
    });
  },
  
  // Get all images with optional status filter
  getAll: async (status = null) => {
    const query = status ? `?status=${status}` : '';
    return fetchAPI(`/images${query}`);
  },
  
  // Get single image
  getById: async (id) => {
    return fetchAPI(`/images/${id}`);
  },
  
  // Approve image
  approve: async (id) => {
    return fetchAPI(`/images/${id}/approve`, {
      method: 'PATCH',
    });
  },
  
  // Reject image
  reject: async (id) => {
    return fetchAPI(`/images/${id}/reject`, {
      method: 'DELETE',
    });
  },
};

