// Subjects API
export const subjectsAPI = {
  // Get all subjects with optional filtering
  getAll: async (grade = null, name = null) => {
    let query = '';
    const params = [];
    if (grade) params.push(`grade=${grade}`);
    if (name) params.push(`name=${encodeURIComponent(name)}`);
    if (params.length > 0) query = `?${params.join('&')}`;
    return fetchAPI(`/subjects${query}`);
  },

  // Get distinct subject names
  getSubjectNames: async () => {
    return fetchAPI('/subjects/names');
  },

  // Get units by grade and subject name
  getUnits: async (grade, name) => {
    return fetchAPI(`/subjects/units?grade=${grade}&name=${encodeURIComponent(name)}`);
  },

  // Get topics by grade, subject name, and unit
  getTopics: async (grade, name, unit) => {
    return fetchAPI(`/subjects/topics?grade=${grade}&name=${encodeURIComponent(name)}&unit=${encodeURIComponent(unit)}`);
  },

  // Get grades by subject name
  getGradesBySubject: async (name) => {
    return fetchAPI(`/subjects/grades?name=${encodeURIComponent(name)}`);
  },
  
  // Create new subject
  create: async (subjectData) => {
    return fetchAPI('/subjects', {
      method: 'POST',
      body: JSON.stringify(subjectData),
    });
  },
};

