// Homework API
export const homeworkAPI = {
  // Get students list (for assignment)
  getStudents: async () => {
    return fetchAPI('/homework/students');
  },

  // Assign exam to students (supports studentIds array or classroomId)
  assign: async (examId, studentIds, dueDate, classroomId = null) => {
    const body = { examId, dueDate };
    if (classroomId) {
      body.classroomId = classroomId;
    } else {
      body.studentIds = studentIds;
    }
    return fetchAPI('/homework/assign', {
      method: 'POST',
      body: JSON.stringify(body),
    });
  },

  // Get teacher's assigned homeworks
  getTeacherHomeworks: async (status = null, examId = null) => {
    const params = [];
    if (status) params.push(`status=${status}`);
    if (examId) params.push(`examId=${examId}`);
    const query = params.length > 0 ? `?${params.join('&')}` : '';
    return fetchAPI(`/homework/teacher${query}`);
  },

  // Get student's homeworks
  getStudentHomeworks: async (status = null) => {
    const query = status ? `?status=${status}` : '';
    return fetchAPI(`/homework/student${query}`);
  },

  // Get homework by ID
  getById: async (id) => {
    return fetchAPI(`/homework/${id}`);
  },

  // Submit homework answers
  submit: async (id, answers) => {
    return fetchAPI(`/homework/${id}/submit`, {
      method: 'PATCH',
      body: JSON.stringify({ answers }),
    });
  },

  // Grade homework
  grade: async (id, grades, feedback) => {
    return fetchAPI(`/homework/${id}/grade`, {
      method: 'PATCH',
      body: JSON.stringify({ grades, feedback }),
    });
  },
};

