import React, { useState } from 'react';
import { X, TestTube2 } from 'lucide-react';

const FloatingTestUsers = ({ onSelectUser }) => {
  const [isOpen, setIsOpen] = useState(false);

  // Test user credentials for easy access
  const testUsers = [
    { email: 'admin@bilgen.com', password: 'admin123', role: 'ADMIN', name: 'Admin User' },
    { email: 'teacher@bilgen.com', password: 'teacher123', role: 'TEACHER', name: 'retmen Ahmet' },
    { email: 'student1@bilgen.com', password: 'student123', role: 'STUDENT', name: 'renci Aye' },
    { email: 'student2@bilgen.com', password: 'student123', role: 'STUDENT', name: 'renci Mehmet' },
    { email: 'student3@bilgen.com', password: 'student123', role: 'STUDENT', name: 'renci Zeynep' }
  ];

  const handleSelectUser = (user) => {
    onSelectUser(user);
    setIsOpen(false);
  };

  return (
    <>
      {/* Floating Toggle Button */}
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="fixed bottom-6 right-6 bg-blue-600 hover:bg-blue-700 text-white rounded-full p-4 shadow-lg transition-all duration-200 hover:scale-110 z-50"
        title="Test Kullanclar"
      >
        <TestTube2 className="h-6 w-6" />
      </button>

      {/* Floating Panel */}
      {isOpen && (
        <div className="fixed bottom-24 right-6 w-96 bg-white rounded-lg shadow-2xl border border-gray-200 z-50 overflow-hidden">
          {/* Header */}
          <div className="bg-blue-600 text-white px-4 py-3 flex items-center justify-between">
            <div className="flex items-center gap-2">
              <TestTube2 className="h-5 w-5" />
              <h3 className="font-semibold">Test Kullanclar</h3>
            </div>
            <button
              onClick={() => setIsOpen(false)}
              className="hover:bg-blue-700 rounded p-1 transition-colors"
            >
              <X className="h-5 w-5" />
            </button>
          </div>

          {/* User List */}
          <div className="p-4 max-h-96 overflow-y-auto">
            <p className="text-sm text-gray-600 mb-3">
              Hzl giri iin bir kullanc sein:
            </p>
            <div className="space-y-2">
              {testUsers.map((user, index) => (
                <button
                  key={index}
                  onClick={() => handleSelectUser(user)}
                  className="w-full text-left p-3 border border-gray-200 rounded-lg hover:border-blue-500 hover:bg-blue-50 transition-all duration-200 group"
                >
                  <div className="flex items-center justify-between mb-1">
                    <span className="font-semibold text-gray-900 group-hover:text-blue-600">
                      {user.role}
                    </span>
                    <span className="text-xs text-gray-500 group-hover:text-blue-500">
                      {user.name}
                    </span>
                  </div>
                  <div className="text-xs text-gray-500 group-hover:text-blue-500">
                    {user.email}
                  </div>
                </button>
              ))}
            </div>
          </div>

          {/* Footer */}
          <div className="bg-gray-50 px-4 py-2 border-t border-gray-200">
            <p className="text-xs text-gray-500 text-center">
              Gelitirme ortam iin test kullanclar
            </p>
          </div>
        </div>
      )}

      {/* Backdrop */}
      {isOpen && (
        <div
          onClick={() => setIsOpen(false)}
          className="fixed inset-0 bg-black bg-opacity-20 z-40"
        />
      )}
    </>
  );
};

export default FloatingTestUsers;

