import React, { useState } from 'react';
import { Navigate, useLocation, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { Loader2, Sparkles, Mail, Lock, Eye, EyeOff } from 'lucide-react';
import { toast } from 'sonner';

import useAuthStore from '../store/authStore';
import { Button } from '../components/ui/Button';
import { Input } from '../components/ui/Input';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../components/ui/Card';
import FloatingTestUsers from '../components/FloatingTestUsers';
import { getDefaultRoute } from '../lib/routeHelpers';

// Form validation schema
const loginSchema = z.object({
  email: z.string().email('Geerli bir email adresi giriniz'),
  password: z.string().min(6, 'ifre en az 6 karakter olmaldr'),
});

const LoginPage = () => {
  const [showPassword, setShowPassword] = useState(false);
  const { isAuthenticated, login, user } = useAuthStore();
  const location = useLocation();
  const navigate = useNavigate();

  const defaultRoute = getDefaultRoute(user);
  const from = location.state?.from?.pathname || defaultRoute;

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
    setValue
  } = useForm({
    resolver: zodResolver(loginSchema),
    defaultValues: {
      email: 'admin@bilgen.com',
      password: 'admin123'
    }
  });

  const onSubmit = async (data) => {
    try {
      const response = await fetch(`${process.env.REACT_APP_API_URL || 'http://localhost:5000/api'}/auth/login`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      });

      const result = await response.json();

      if (response.ok) {
        login(result.user, result.token);
        toast.success('Giri baarl! Ynlendiriliyorsunuz...');
        const destination = location.state?.from?.pathname || getDefaultRoute(result.user);
        navigate(destination, { replace: true });
      } else {
        toast.error(result.message || 'Giri yaplrken bir hata olutu');
      }
    } catch (error) {
      console.error('Login error:', error);
      toast.error('Balant hatas. Ltfen tekrar deneyin.');
    }
  };

  // Handler for FloatingTestUsers
  const handleSelectTestUser = (user) => {
    setValue('email', user.email);
    setValue('password', user.password);
    toast.info(`${user.role} kullancs seildi`);
  };

  // If already authenticated, redirect
  if (isAuthenticated) {
    return <Navigate to={from} replace />;
  }

  return (
    <div className="min-h-screen flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="max-w-md w-full"
      >
        {/* Logo and Header */}
        <div className="text-center mb-8">
          <motion.div
            initial={{ scale: 0.8 }}
            animate={{ scale: 1 }}
            transition={{ delay: 0.2, duration: 0.3 }}
            className="mx-auto h-16 w-16 bg-gradient-to-r from-indigo-500 to-purple-500 rounded-2xl flex items-center justify-center mb-4"
          >
            <Sparkles className="h-8 w-8 text-white" />
          </motion.div>

          <h2 className="text-3xl font-bold bg-gradient-to-r from-indigo-600 to-purple-600 bg-clip-text text-transparent">
            BilGen AI
          </h2>
          <p className="mt-2 text-gray-600">
            Eitim grsellerinizi oluturmak iin giri yapn
          </p>
        </div>

        {/* Login Form */}
        <Card className="bg-white/80 backdrop-blur-sm border-0 shadow-xl">
          <CardHeader className="space-y-1">
            <CardTitle className="text-2xl text-center">Giri Yapn</CardTitle>
            <CardDescription className="text-center">
              Hesabnzla devam etmek iin bilgilerinizi giriniz
            </CardDescription>
          </CardHeader>

          <CardContent>
            <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
              {/* Email Field */}
              <div className="space-y-2">
                <label className="text-sm font-medium text-gray-700">Email</label>
                <div className="relative">
                  <Mail className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                  <Input
                    {...register('email')}
                    type="email"
                    placeholder="admin@bilgen.com"
                    className="pl-10"
                    disabled={isSubmitting}
                  />
                </div>
                {errors.email && (
                  <p className="text-sm text-red-500">{errors.email.message}</p>
                )}
              </div>

              {/* Password Field */}
              <div className="space-y-2">
                <label className="text-sm font-medium text-gray-700">ifre</label>
                <div className="relative">
                  <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                  <Input
                    {...register('password')}
                    type={showPassword ? 'text' : 'password'}
                    placeholder="admin123"
                    className="pl-10 pr-10"
                    disabled={isSubmitting}
                  />
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    className="absolute right-3 top-3 h-4 w-4 text-gray-400 hover:text-gray-600"
                  >
                    {showPassword ? <EyeOff /> : <Eye />}
                  </button>
                </div>
                {errors.password && (
                  <p className="text-sm text-red-500">{errors.password.message}</p>
                )}
              </div>

              {/* Submit Button */}
              <Button
                type="submit"
                className="w-full bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-700 hover:to-purple-700"
                disabled={isSubmitting}
              >
                {isSubmitting ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Giri yaplyor...
                  </>
                ) : (
                  'Giri Yap'
                )}
              </Button>
            </form>
          </CardContent>
        </Card>

        {/* Footer */}
        <motion.p
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.5 }}
          className="mt-8 text-center text-sm text-gray-500"
        >
          BilGen AI - AI destekli eitim grselleri platformu
        </motion.p>
      </motion.div>

      {/* Floating Test Users Panel */}
      <FloatingTestUsers onSelectUser={handleSelectTestUser} />
    </div>
  );
};

export default LoginPage;