import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useQuery } from '@tanstack/react-query';
import {
  ImageIcon,
  Download,
  Eye,
  Filter,
  Search,
  X,
  Calendar,
  Tag
} from 'lucide-react';
import { toast } from 'sonner';

import { Card, CardContent } from '../components/ui/Card';
import { Button } from '../components/ui/Button';
import { Input } from '../components/ui/Input';
import { Tabs, TabsList, TabsTrigger } from '../components/ui/Tabs';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '../components/ui/Dialog';
import { Skeleton } from '../components/ui/Skeleton';
import { imagesAPI, getImageUrl } from '../services/api';

function GalleryPage() {
  const [selectedGrade, setSelectedGrade] = useState('all');
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedImage, setSelectedImage] = useState(null);

  // Fetch approved images
  const { data: images = [], isLoading, error } = useQuery({
    queryKey: ['images', 'APPROVED'],
    queryFn: () => imagesAPI.getAll('APPROVED'),
    select: (response) => response.data || []
  });

  // Filter images based on grade and search
  const filteredImages = images.filter(image => {
    const matchesGrade = selectedGrade === 'all' ||
      image.metadata?.snf === selectedGrade ||
      image.subject?.grade === parseInt(selectedGrade);

    const matchesSearch = !searchQuery ||
      image.prompt.toLowerCase().includes(searchQuery.toLowerCase()) ||
      image.metadata?.konu?.toLowerCase().includes(searchQuery.toLowerCase()) ||
      image.metadata?.nite?.toLowerCase().includes(searchQuery.toLowerCase());

    return matchesGrade && matchesSearch;
  });

  const handleDownload = (image, event) => {
    event.stopPropagation();
    try {
      const link = document.createElement('a');
      link.href = getImageUrl(image.filePath);
      link.download = `bilgen_${image.id}_${image.metadata?.snf || 'image'}.jpg`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      toast.success('Grsel indiriliyor...');
    } catch (error) {
      toast.error('ndirme srasnda hata olutu');
    }
  };

  const grades = ['all', '1', '2', '3', '4', '5', '6', '7', '8'];
  const gradeLabels = {
    'all': 'Tm',
    '1': '1. Snf',
    '2': '2. Snf',
    '3': '3. Snf',
    '4': '4. Snf',
    '5': '5. Snf',
    '6': '6. Snf',
    '7': '7. Snf',
    '8': '8. Snf'
  };

  if (error) {
    return (
      <div className="text-center py-12">
        <div className="bg-red-50 border border-red-200 rounded-lg p-6 max-w-md mx-auto">
          <h3 className="text-lg font-medium text-red-800 mb-2">Hata Olutu</h3>
          <p className="text-red-600">Grseller yklenirken bir hata olutu.</p>
          <Button
            variant="outline"
            onClick={() => window.location.reload()}
            className="mt-4"
          >
            Tekrar Dene
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto space-y-6">
      {/* Header */}
      <motion.div
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        className="text-center"
      >
        <div className="flex items-center justify-center mb-4">
          <div className="h-12 w-12 bg-gradient-to-r from-blue-500 to-cyan-500 rounded-xl flex items-center justify-center mr-3">
            <ImageIcon className="h-6 w-6 text-white" />
          </div>
          <h1 className="text-3xl font-bold text-gray-900">Grsel Galerisi</h1>
        </div>
        <p className="text-gray-600">
          Onaylanm eitim grsellerini kefedin ve indirin
        </p>
      </motion.div>

      {/* Filters */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.1 }}
      >
        <Card>
          <CardContent className="p-6">
            <div className="flex flex-col md:flex-row gap-4 items-center">
              {/* Search */}
              <div className="relative flex-1 max-w-md">
                <Search className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                <Input
                  placeholder="Grsellerde ara..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="pl-10"
                />
              </div>

              {/* Grade Filter */}
              <div className="flex items-center space-x-2">
                <Filter className="h-4 w-4 text-gray-500" />
                <Tabs value={selectedGrade} onValueChange={setSelectedGrade}>
                  <TabsList className="grid grid-cols-9 gap-1">
                    {grades.map((grade) => (
                      <TabsTrigger
                        key={grade}
                        value={grade}
                        className="text-xs px-2 py-1"
                      >
                        {gradeLabels[grade]}
                      </TabsTrigger>
                    ))}
                  </TabsList>
                </Tabs>
              </div>
            </div>

            {/* Results Info */}
            <div className="flex items-center justify-between mt-4 pt-4 border-t">
              <p className="text-sm text-gray-600">
                {filteredImages.length} grsel bulundu
                {selectedGrade !== 'all' && ` (${gradeLabels[selectedGrade]})`}
              </p>
            </div>
          </CardContent>
        </Card>
      </motion.div>

      {/* Image Grid */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.2 }}
      >
        {isLoading ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            {Array.from({ length: 8 }).map((_, i) => (
              <Card key={i}>
                <CardContent className="p-4">
                  <Skeleton className="aspect-square w-full rounded-lg mb-4" />
                  <Skeleton className="h-4 w-3/4 mb-2" />
                  <Skeleton className="h-3 w-1/2" />
                </CardContent>
              </Card>
            ))}
          </div>
        ) : filteredImages.length === 0 ? (
          <div className="text-center py-12">
            <ImageIcon className="h-16 w-16 text-gray-300 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-gray-900 mb-2">Grsel Bulunamad</h3>
            <p className="text-gray-600">
              {searchQuery || selectedGrade !== 'all'
                ? 'Arama kriterlerinize uygun grsel bulunamad.'
                : 'Henz onaylanm grsel bulunmuyor.'}
            </p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            <AnimatePresence>
              {filteredImages.map((image, index) => (
                <motion.div
                  key={image.id}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                  transition={{ delay: index * 0.05 }}
                  layout
                >
                  <Card className="group hover:shadow-lg transition-all duration-300 cursor-pointer">
                    <CardContent className="p-0">
                      {/* Image */}
                      <div
                        className="relative aspect-square bg-gray-100 rounded-t-lg overflow-hidden"
                        onClick={() => setSelectedImage(image)}
                      >
                        <img
                          src={getImageUrl(image.filePath)}
                          alt={image.prompt}
                          className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                        />

                        {/* Overlay */}
                        <div className="absolute inset-0 bg-black/0 group-hover:bg-black/20 transition-colors duration-300 flex items-center justify-center">
                          <div className="opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex space-x-2">
                            <Button
                              size="sm"
                              variant="secondary"
                              onClick={(e) => {
                                e.stopPropagation();
                                setSelectedImage(image);
                              }}
                            >
                              <Eye className="h-4 w-4" />
                            </Button>
                            <Button
                              size="sm"
                              variant="secondary"
                              onClick={(e) => handleDownload(image, e)}
                            >
                              <Download className="h-4 w-4" />
                            </Button>
                          </div>
                        </div>

                        {/* Grade Badge */}
                        {image.metadata?.snf && (
                          <div className="absolute top-2 left-2 bg-blue-500 text-white text-xs px-2 py-1 rounded-full">
                            {image.metadata.snf}. Snf
                          </div>
                        )}
                      </div>

                      {/* Content */}
                      <div className="p-4">
                        <h3 className="font-medium text-gray-900 mb-2 line-clamp-2">
                          {image.prompt.length > 60
                            ? `${image.prompt.substring(0, 60)}...`
                            : image.prompt}
                        </h3>

                        <div className="space-y-1">
                          {image.metadata?.konu && (
                            <div className="flex items-center text-xs text-gray-500">
                              <Tag className="h-3 w-3 mr-1" />
                              {image.metadata.konu}
                            </div>
                          )}

                          {image.metadata?.nite && (
                            <div className="flex items-center text-xs text-gray-500">
                              <Tag className="h-3 w-3 mr-1" />
                              {image.metadata.nite}
                            </div>
                          )}

                          <div className="flex items-center text-xs text-gray-400 mt-2">
                            <Calendar className="h-3 w-3 mr-1" />
                            {new Date(image.createdAt).toLocaleDateString('tr-TR')}
                          </div>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                </motion.div>
              ))}
            </AnimatePresence>
          </div>
        )}
      </motion.div>

      {/* Image Detail Modal */}
      <Dialog open={!!selectedImage} onOpenChange={() => setSelectedImage(null)}>
        <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
          {selectedImage && (
            <>
              <DialogHeader>
                <DialogTitle className="flex items-center justify-between">
                  Grsel Detaylar
                  <Button
                    variant="ghost"
                    size="sm"
                    onClick={() => setSelectedImage(null)}
                  >
                    <X className="h-4 w-4" />
                  </Button>
                </DialogTitle>
              </DialogHeader>

              <div className="grid md:grid-cols-2 gap-6">
                {/* Image */}
                <div className="space-y-4">
                  <div className="aspect-square bg-gray-100 rounded-lg overflow-hidden">
                    <img
                      src={getImageUrl(selectedImage.filePath)}
                      alt={selectedImage.prompt}
                      className="w-full h-full object-cover"
                    />
                  </div>

                  <Button
                    onClick={(e) => handleDownload(selectedImage, e)}
                    className="w-full bg-gradient-to-r from-blue-600 to-cyan-600 hover:from-blue-700 hover:to-cyan-700"
                  >
                    <Download className="mr-2 h-4 w-4" />
                    Grseli ndir
                  </Button>
                </div>

                {/* Details */}
                <div className="space-y-6">
                  <div>
                    <h3 className="font-semibold text-lg mb-2">Aklama</h3>
                    <p className="text-gray-700">{selectedImage.prompt}</p>
                  </div>

                  {selectedImage.metadata && (
                    <div>
                      <h3 className="font-semibold text-lg mb-3">Eitim Bilgileri</h3>
                      <div className="space-y-3">
                        {selectedImage.metadata.snf && (
                          <div className="flex items-center">
                            <span className="font-medium text-gray-700 w-20">Snf:</span>
                            <span className="text-gray-600">{selectedImage.metadata.snf}</span>
                          </div>
                        )}

                        {selectedImage.metadata.nite && (
                          <div className="flex items-center">
                            <span className="font-medium text-gray-700 w-20">nite:</span>
                            <span className="text-gray-600">{selectedImage.metadata.nite}</span>
                          </div>
                        )}

                        {selectedImage.metadata.konu && (
                          <div className="flex items-center">
                            <span className="font-medium text-gray-700 w-20">Konu:</span>
                            <span className="text-gray-600">{selectedImage.metadata.konu}</span>
                          </div>
                        )}

                        {selectedImage.metadata.kazanm && (
                          <div>
                            <span className="font-medium text-gray-700">Kazanm:</span>
                            <p className="text-gray-600 mt-1">{selectedImage.metadata.kazanm}</p>
                          </div>
                        )}
                      </div>
                    </div>
                  )}

                  <div>
                    <h3 className="font-semibold text-lg mb-3">Sistem Bilgileri</h3>
                    <div className="space-y-2 text-sm text-gray-600">
                      <div className="flex items-center">
                        <Calendar className="h-4 w-4 mr-2" />
                        <span>Oluturulma: {new Date(selectedImage.createdAt).toLocaleString('tr-TR')}</span>
                      </div>

                      <div className="flex items-center">
                        <span className="w-2 h-2 bg-green-500 rounded-full mr-2"></span>
                        <span>Durum: Onayland</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </>
          )}
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default GalleryPage;