import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { toast } from 'sonner';
import {
  BookOpen,
  Clock,
  CheckCircle,
  XCircle,
  AlertCircle,
  ChevronRight,
  ChevronLeft,
  Send,
  Trophy,
  Calendar,
  FileText,
  GraduationCap
} from 'lucide-react';

import { homeworkAPI } from '../services/api';
import { Button } from '../components/ui/Button';
import { Card, CardContent } from '../components/ui/Card';

// Status Badge Component
function StatusBadge({ status }) {
  const config = {
    ASSIGNED: { label: 'Bekliyor', color: 'bg-yellow-100 text-yellow-700', icon: Clock },
    SUBMITTED: { label: 'Gnderildi', color: 'bg-blue-100 text-blue-700', icon: Send },
    GRADED: { label: 'Puanland', color: 'bg-green-100 text-green-700', icon: CheckCircle }
  };

  const { label, color, icon: Icon } = config[status] || config.ASSIGNED;

  return (
    <span className={`inline-flex items-center space-x-1 px-2 py-1 rounded-full text-xs font-medium ${color}`}>
      <Icon className="h-3 w-3" />
      <span>{label}</span>
    </span>
  );
}

// Homework Card Component
function HomeworkCard({ homework, onClick }) {
  const isOverdue = homework.dueDate && new Date(homework.dueDate) < new Date() && homework.status === 'ASSIGNED';

  return (
    <motion.div
      whileHover={{ scale: 1.01 }}
      whileTap={{ scale: 0.99 }}
      className={`bg-white border rounded-lg p-4 cursor-pointer transition-shadow hover:shadow-md ${
        isOverdue ? 'border-red-200 bg-red-50' : ''
      }`}
      onClick={onClick}
    >
      <div className="flex items-start justify-between">
        <div className="flex-1">
          <div className="flex items-center space-x-2 mb-1">
            <h3 className="font-semibold text-gray-900">{homework.exam?.title}</h3>
            <StatusBadge status={homework.status} />
          </div>
          <p className="text-sm text-gray-600">
            {homework.exam?.subjectName}  {homework.exam?.grade}. Snf
          </p>

          <div className="flex items-center space-x-4 mt-3 text-sm text-gray-500">
            {homework.dueDate && (
              <span className={`flex items-center space-x-1 ${isOverdue ? 'text-red-600' : ''}`}>
                <Calendar className="h-4 w-4" />
                <span>
                  {isOverdue ? 'Sresi geti: ' : 'Son tarih: '}
                  {new Date(homework.dueDate).toLocaleDateString('tr-TR')}
                </span>
              </span>
            )}
            {homework.exam?.duration && (
              <span className="flex items-center space-x-1">
                <Clock className="h-4 w-4" />
                <span>{homework.exam.duration} dk</span>
              </span>
            )}
          </div>
        </div>

        <div className="flex items-center">
          {homework.status === 'GRADED' && homework.score !== null && (
            <div className="text-right mr-4">
              <p className="text-2xl font-bold text-indigo-600">{homework.score}</p>
              <p className="text-xs text-gray-500">/ {homework.exam?.totalPoints}</p>
            </div>
          )}
          <ChevronRight className="h-5 w-5 text-gray-400" />
        </div>
      </div>
    </motion.div>
  );
}

// Exam Runner Component
function ExamRunner({ homework, onSubmit, onBack }) {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [answers, setAnswers] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const questions = homework.exam?.questions || [];
  const currentQuestion = questions[currentIndex];
  const isLastQuestion = currentIndex === questions.length - 1;
  const isFirstQuestion = currentIndex === 0;

  const handleAnswer = (questionId, answer) => {
    setAnswers(prev => ({
      ...prev,
      [questionId]: answer
    }));
  };

  const handleNext = () => {
    if (!isLastQuestion) {
      setCurrentIndex(prev => prev + 1);
    }
  };

  const handlePrev = () => {
    if (!isFirstQuestion) {
      setCurrentIndex(prev => prev - 1);
    }
  };

  const handleSubmit = async () => {
    const unansweredCount = questions.filter(q => !answers[q.id]).length;
    if (unansweredCount > 0) {
      if (!window.confirm(`${unansweredCount} soru cevaplanmad. Yine de gndermek istiyor musunuz?`)) {
        return;
      }
    }

    setIsSubmitting(true);
    try {
      const formattedAnswers = questions.map(q => ({
        questionId: q.id,
        answer: answers[q.id] || ''
      }));

      await onSubmit(formattedAnswers);
    } finally {
      setIsSubmitting(false);
    }
  };

  const answeredCount = Object.keys(answers).filter(k => answers[k]).length;
  const progress = (answeredCount / questions.length) * 100;

  return (
    <div className="space-y-4">
      {/* Header */}
      <Card>
        <CardContent className="py-4">
          <div className="flex items-center justify-between">
            <div>
              <h2 className="font-semibold text-gray-900">{homework.exam?.title}</h2>
              <p className="text-sm text-gray-600">
                {homework.exam?.subjectName}  {homework.exam?.grade}. Snf
              </p>
            </div>
            <Button variant="outline" onClick={onBack}>
              <ChevronLeft className="h-4 w-4 mr-1" />
              Geri
            </Button>
          </div>

          {/* Progress Bar */}
          <div className="mt-4">
            <div className="flex justify-between text-sm text-gray-600 mb-1">
              <span>lerleme</span>
              <span>{answeredCount}/{questions.length} soru cevapland</span>
            </div>
            <div className="w-full h-2 bg-gray-200 rounded-full overflow-hidden">
              <div
                className="h-full bg-indigo-600 transition-all duration-300"
                style={{ width: `${progress}%` }}
              />
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Question Navigation */}
      <div className="flex flex-wrap gap-2">
        {questions.map((q, index) => (
          <button
            key={q.id}
            onClick={() => setCurrentIndex(index)}
            className={`w-10 h-10 rounded-lg text-sm font-medium transition-all ${
              index === currentIndex
                ? 'bg-indigo-600 text-white'
                : answers[q.id]
                  ? 'bg-green-100 text-green-700'
                  : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
            }`}
          >
            {index + 1}
          </button>
        ))}
      </div>

      {/* Current Question */}
      {currentQuestion && (
        <motion.div
          key={currentQuestion.id}
          initial={{ opacity: 0, x: 20 }}
          animate={{ opacity: 1, x: 0 }}
          className="bg-white border rounded-lg p-6"
        >
          <div className="flex items-center space-x-2 mb-4">
            <span className="bg-indigo-100 text-indigo-700 px-3 py-1 rounded-full text-sm font-medium">
              Soru {currentIndex + 1}
            </span>
            <span className={`px-2 py-1 rounded text-xs ${
              currentQuestion.type === 'multiple_choice'
                ? 'bg-blue-100 text-blue-700'
                : 'bg-green-100 text-green-700'
            }`}>
              {currentQuestion.type === 'multiple_choice' ? 'oktan Semeli' : 'Ak Ulu'}
            </span>
            <span className="text-sm text-gray-500">{currentQuestion.points} puan</span>
          </div>

          <p className="text-lg text-gray-900 mb-6">{currentQuestion.question}</p>

          {/* Answer Input */}
          {currentQuestion.type === 'multiple_choice' ? (
            <div className="space-y-2">
              {currentQuestion.options?.map((option, i) => {
                const optionLetter = option.charAt(0);
                const isSelected = answers[currentQuestion.id] === optionLetter;

                return (
                  <button
                    key={i}
                    onClick={() => handleAnswer(currentQuestion.id, optionLetter)}
                    className={`w-full text-left p-4 rounded-lg border transition-all ${
                      isSelected
                        ? 'border-indigo-500 bg-indigo-50 text-indigo-900'
                        : 'border-gray-200 hover:border-gray-300 hover:bg-gray-50'
                    }`}
                  >
                    {option}
                  </button>
                );
              })}
            </div>
          ) : (
            <textarea
              value={answers[currentQuestion.id] || ''}
              onChange={(e) => handleAnswer(currentQuestion.id, e.target.value)}
              placeholder="Cevabnz buraya yazn..."
              rows={6}
              className="w-full p-4 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent resize-none"
            />
          )}
        </motion.div>
      )}

      {/* Navigation Buttons */}
      <div className="flex justify-between">
        <Button
          variant="outline"
          onClick={handlePrev}
          disabled={isFirstQuestion}
        >
          <ChevronLeft className="h-4 w-4 mr-1" />
          nceki
        </Button>

        {isLastQuestion ? (
          <Button
            onClick={handleSubmit}
            disabled={isSubmitting}
            className="bg-green-600 hover:bg-green-700"
          >
            {isSubmitting ? (
              <>
                <div className="animate-spin rounded-full h-4 w-4 border-2 border-white border-t-transparent mr-2" />
                Gnderiliyor...
              </>
            ) : (
              <>
                <Send className="h-4 w-4 mr-1" />
                Snav Gnder
              </>
            )}
          </Button>
        ) : (
          <Button onClick={handleNext}>
            Sonraki
            <ChevronRight className="h-4 w-4 ml-1" />
          </Button>
        )}
      </div>
    </div>
  );
}

// Results View Component
function ResultsView({ homework, onBack }) {
  const questions = homework.exam?.questions || [];
  const studentAnswers = homework.studentAnswers || [];

  const getAnswerForQuestion = (questionId) => {
    return studentAnswers.find(a => a.questionId === questionId);
  };

  return (
    <div className="space-y-4">
      {/* Header */}
      <Card>
        <CardContent className="py-4">
          <div className="flex items-center justify-between">
            <div>
              <h2 className="font-semibold text-gray-900">{homework.exam?.title}</h2>
              <p className="text-sm text-gray-600">
                {homework.exam?.subjectName}  {homework.exam?.grade}. Snf
              </p>
            </div>
            <Button variant="outline" onClick={onBack}>
              <ChevronLeft className="h-4 w-4 mr-1" />
              Geri
            </Button>
          </div>

          {/* Score Card */}
          {homework.status === 'GRADED' && (
            <div className="mt-4 p-4 bg-gradient-to-r from-indigo-500 to-purple-500 rounded-lg text-white">
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-3">
                  <Trophy className="h-8 w-8" />
                  <div>
                    <p className="text-sm opacity-80">Puannz</p>
                    <p className="text-3xl font-bold">{homework.score} / {homework.exam?.totalPoints}</p>
                  </div>
                </div>
                <div className="text-right">
                  <p className="text-4xl font-bold">
                    %{Math.round((homework.score / homework.exam?.totalPoints) * 100)}
                  </p>
                </div>
              </div>
            </div>
          )}

          {homework.feedback && (
            <div className="mt-4 p-3 bg-blue-50 rounded-lg">
              <p className="text-sm font-medium text-blue-900">retmen Yorumu:</p>
              <p className="text-sm text-blue-700">{homework.feedback}</p>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Questions Review */}
      <div className="space-y-4">
        {questions.map((question, index) => {
          const answer = getAnswerForQuestion(question.id);
          const isCorrect = answer?.isCorrect;
          const isMultipleChoice = question.type === 'multiple_choice';

          return (
            <div
              key={question.id}
              className={`bg-white border rounded-lg p-4 ${
                isCorrect === true
                  ? 'border-green-200'
                  : isCorrect === false
                    ? 'border-red-200'
                    : ''
              }`}
            >
              <div className="flex items-center space-x-2 mb-3">
                <span className="bg-gray-100 text-gray-700 px-2 py-1 rounded text-sm font-medium">
                  Soru {index + 1}
                </span>
                {isCorrect === true && (
                  <span className="flex items-center text-green-600 text-sm">
                    <CheckCircle className="h-4 w-4 mr-1" />
                    Doru
                  </span>
                )}
                {isCorrect === false && (
                  <span className="flex items-center text-red-600 text-sm">
                    <XCircle className="h-4 w-4 mr-1" />
                    Yanl
                  </span>
                )}
                {isCorrect === null && (
                  <span className="flex items-center text-yellow-600 text-sm">
                    <AlertCircle className="h-4 w-4 mr-1" />
                    Deerlendirilecek
                  </span>
                )}
                <span className="text-sm text-gray-500 ml-auto">
                  {answer?.points ?? '-'} / {question.points} puan
                </span>
              </div>

              <p className="text-gray-900 mb-3">{question.question}</p>

              {/* Student Answer */}
              <div className="space-y-2">
                <div className={`p-3 rounded-lg ${
                  isCorrect === true
                    ? 'bg-green-50'
                    : isCorrect === false
                      ? 'bg-red-50'
                      : 'bg-gray-50'
                }`}>
                  <p className="text-sm font-medium text-gray-700">Cevabnz:</p>
                  <p className="text-gray-900">{answer?.answer || 'Cevaplanmad'}</p>
                </div>

                {/* Correct Answer (for graded multiple choice) */}
                {isMultipleChoice && question.correctAnswer && homework.status === 'GRADED' && (
                  <div className="p-3 bg-green-50 rounded-lg">
                    <p className="text-sm font-medium text-green-700">Doru Cevap:</p>
                    <p className="text-green-900">{question.correctAnswer}</p>
                    {question.explanation && (
                      <p className="text-sm text-green-700 mt-1">{question.explanation}</p>
                    )}
                  </div>
                )}

                {/* Teacher Comment */}
                {answer?.teacherComment && (
                  <div className="p-3 bg-blue-50 rounded-lg">
                    <p className="text-sm font-medium text-blue-700">retmen Notu:</p>
                    <p className="text-blue-900">{answer.teacherComment}</p>
                  </div>
                )}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

// Main Student Page Component
function StudentPage() {
  const [homeworks, setHomeworks] = useState([]);
  const [selectedHomework, setSelectedHomework] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [activeFilter, setActiveFilter] = useState('all');

  useEffect(() => {
    loadHomeworks();
  }, []);

  const loadHomeworks = async () => {
    setIsLoading(true);
    try {
      const response = await homeworkAPI.getStudentHomeworks();
      setHomeworks(response.data || []);
    } catch (error) {
      toast.error('devler yklenemedi');
    } finally {
      setIsLoading(false);
    }
  };

  const handleSelectHomework = async (homework) => {
    try {
      const response = await homeworkAPI.getById(homework.id);
      setSelectedHomework(response.data);
    } catch (error) {
      toast.error('dev detaylar yklenemedi');
    }
  };

  const handleSubmitExam = async (answers) => {
    try {
      const response = await homeworkAPI.submit(selectedHomework.id, answers);
      toast.success(response.message || 'Snav gnderildi!');
      setSelectedHomework(null);
      loadHomeworks();
    } catch (error) {
      toast.error(error.message || 'Gnderim baarsz');
    }
  };

  const filteredHomeworks = homeworks.filter(hw => {
    if (activeFilter === 'all') return true;
    return hw.status === activeFilter;
  });

  const pendingCount = homeworks.filter(h => h.status === 'ASSIGNED').length;
  const completedCount = homeworks.filter(h => h.status !== 'ASSIGNED').length;

  // Show exam runner if homework is selected and in ASSIGNED status
  if (selectedHomework?.status === 'ASSIGNED') {
    return (
      <ExamRunner
        homework={selectedHomework}
        onSubmit={handleSubmitExam}
        onBack={() => setSelectedHomework(null)}
      />
    );
  }

  // Show results if homework is submitted or graded
  if (selectedHomework && selectedHomework.status !== 'ASSIGNED') {
    return (
      <ResultsView
        homework={selectedHomework}
        onBack={() => setSelectedHomework(null)}
      />
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
          <GraduationCap className="h-7 w-7 text-indigo-600" />
          devlerim
        </h1>
        <p className="text-gray-600 mt-1">Atanan snavlar grntleyin ve zn</p>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <Card className="bg-gradient-to-br from-yellow-50 to-orange-50 border-yellow-200">
          <CardContent className="py-4">
            <div className="flex items-center space-x-3">
              <Clock className="h-8 w-8 text-yellow-600" />
              <div>
                <p className="text-2xl font-bold text-yellow-900">{pendingCount}</p>
                <p className="text-sm text-yellow-700">Bekleyen</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="bg-gradient-to-br from-green-50 to-emerald-50 border-green-200">
          <CardContent className="py-4">
            <div className="flex items-center space-x-3">
              <CheckCircle className="h-8 w-8 text-green-600" />
              <div>
                <p className="text-2xl font-bold text-green-900">{completedCount}</p>
                <p className="text-sm text-green-700">Tamamlanan</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="bg-gradient-to-br from-blue-50 to-indigo-50 border-blue-200">
          <CardContent className="py-4">
            <div className="flex items-center space-x-3">
              <FileText className="h-8 w-8 text-blue-600" />
              <div>
                <p className="text-2xl font-bold text-blue-900">{homeworks.length}</p>
                <p className="text-sm text-blue-700">Toplam</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="bg-gradient-to-br from-purple-50 to-pink-50 border-purple-200">
          <CardContent className="py-4">
            <div className="flex items-center space-x-3">
              <Trophy className="h-8 w-8 text-purple-600" />
              <div>
                <p className="text-2xl font-bold text-purple-900">
                  {homeworks.filter(h => h.status === 'GRADED' && h.score !== null).length > 0
                    ? Math.round(
                        homeworks
                          .filter(h => h.status === 'GRADED' && h.score !== null)
                          .reduce((sum, h) => sum + (h.score / h.exam?.totalPoints * 100), 0) /
                        homeworks.filter(h => h.status === 'GRADED').length
                      )
                    : '-'}
                </p>
                <p className="text-sm text-purple-700">Ort. Puan %</p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Filter Tabs */}
      <div className="flex space-x-2">
        {[
          { key: 'all', label: 'Tm' },
          { key: 'ASSIGNED', label: 'Bekleyen' },
          { key: 'SUBMITTED', label: 'Gnderilen' },
          { key: 'GRADED', label: 'Puanlanan' }
        ].map(filter => (
          <button
            key={filter.key}
            onClick={() => setActiveFilter(filter.key)}
            className={`px-4 py-2 rounded-lg text-sm font-medium transition-all ${
              activeFilter === filter.key
                ? 'bg-indigo-600 text-white'
                : 'bg-white text-gray-600 hover:bg-gray-50'
            }`}
          >
            {filter.label}
          </button>
        ))}
      </div>

      {/* Homework List */}
      {isLoading ? (
        <div className="text-center py-12">
          <div className="animate-spin h-8 w-8 border-4 border-indigo-600 border-t-transparent rounded-full mx-auto" />
          <p className="text-gray-500 mt-4">Ykleniyor...</p>
        </div>
      ) : filteredHomeworks.length === 0 ? (
        <div className="text-center py-12 bg-white rounded-lg border">
          <BookOpen className="h-12 w-12 text-gray-300 mx-auto mb-4" />
          <p className="text-gray-500">
            {activeFilter === 'all' ? 'Henz dev atanmad' : 'Bu kategoride dev yok'}
          </p>
        </div>
      ) : (
        <div className="space-y-3">
          <AnimatePresence>
            {filteredHomeworks.map(homework => (
              <HomeworkCard
                key={homework.id}
                homework={homework}
                onClick={() => handleSelectHomework(homework)}
              />
            ))}
          </AnimatePresence>
        </div>
      )}
    </div>
  );
}

export default StudentPage;

